import { useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

export const useRealtimeMessages = (setMoments) => {
  useEffect(() => {
    const subscription = supabase
      .channel("public:internal_messages")
      .on(
        "postgres_changes",
        { event: "INSERT", schema: "public", table: "internal_messages" },
        async (payload) => {
          const { data: userData, error: userError } = await supabase
            .from("users")
            .select("username")
            .eq("id", payload.new.user_id)
            .single();

          if (userError) {
            console.error("Error fetching user data:", userError);
            return;
          }

          setMoments((currentMoments) => {
            return currentMoments.map((moment) => {
              if (moment.tribe_id === payload.new.tribe_id) {
                const updatedMessages = [
                  ...moment.recent_messages,
                  {
                    ...payload.new,
                    username: userData.username || "Unknown User",
                  },
                ].slice(-3);
                return { ...moment, recent_messages: updatedMessages };
              }
              return moment;
            });
          });
        }
      )
      .subscribe();

    return () => {
      subscription.unsubscribe();
    };
  }, [setMoments]);
};
