import React from "react";
import { useAuth } from "../contexts/AuthContext";

const QuickActionsButton = () => {
  const { signIn } = useAuth();

  const clearSiteData = () => {
    if (
      window.confirm(
        "Are you sure you want to clear all site data? This will log you out."
      )
    ) {
      localStorage.clear();
      sessionStorage.clear();
      if (window.indexedDB) {
        const databases = window.indexedDB.databases();
        databases.then((dbs) => {
          dbs.forEach((db) => {
            window.indexedDB.deleteDatabase(db.name);
          });
        });
      }
      window.location.reload();
    }
  };

  const quickSignIn = async (username) => {
    try {
      await signIn({ username, password: "123456789" });
    } catch (error) {
      console.error(`Error signing in as ${username}:`, error);
      alert(`Failed to sign in as ${username}. Please try again.`);
    }
  };

  // return (
  //   <div className="fixed bottom-4 right-4 flex flex-col space-y-2">
  //     <button
  //       onClick={() => quickSignIn("lachie")}
  //       className="bg-zinc-950 text-white px-4 py-2 rounded-full shadow-lg hover:bg-zinc-950 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"
  //     >
  //       Sign in as lachie
  //     </button>
  //     <button
  //       onClick={() => quickSignIn("giddy2")}
  //       className="bg-green-500 text-white px-4 py-2 rounded-full shadow-lg hover:bg-green-600 focus:outline-none focus:ring-2 focus:ring-green-500 focus:ring-opacity-50"
  //     >
  //       Sign in as giddy2
  //     </button>
  //     <button
  //       onClick={clearSiteData}
  //       className="bg-red-500 text-white px-4 py-2 rounded-full shadow-lg hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-50"
  //     >
  //       Clear Site Data
  //     </button>
  //   </div>
  // );
};

export default QuickActionsButton;
