import { useRef, useCallback, useState, useEffect } from "react";
import { useAuth } from "../contexts/AuthContext";
import { useChatMessages } from "./useChatMessages";
import { useChatUsers } from "./useChatUsers";
import { useChatSubscription } from "./useChatSubscription";
import { useChatSend } from "./useChatSend";

export const useChatLogic = (type, tribe, otherTribe, onSendMessage) => {
  const { user } = useAuth();
  const messagesEndRef = useRef(null);
  const [error, setError] = useState(null);
  const [isInitialized, setIsInitialized] = useState(false);

  const { users, fetchUserInfo } = useChatUsers();
  const {
    messages,
    setMessages,
    isLoading,
    error: fetchError,
    fetchMessages,
  } = useChatMessages(type, tribe, otherTribe);
  const {
    message,
    setMessage,
    isSending,
    error: sendError,
    handleSend,
    handleKeyPress,
  } = useChatSend(onSendMessage, tribe, otherTribe);

  const handleNewMessage = useCallback(
    async (payload) => {
      const newMessage = payload.new;
      const username = await fetchUserInfo(newMessage.user_id);
      setMessages((prevMessages) => [
        ...prevMessages,
        { ...newMessage, username },
      ]);
    },
    [fetchUserInfo]
  );

  useChatSubscription(type, tribe, otherTribe, handleNewMessage);

  useEffect(() => {
    if (!isInitialized) {
      const initializeChat = async () => {
        try {
          await fetchMessages(fetchUserInfo);
          setIsInitialized(true);
        } catch (error) {
          console.error("Error initializing chat:", error);
          setError("Failed to load messages. Please try again.");
        }
      };

      initializeChat();
    }
  }, [isInitialized, fetchMessages, fetchUserInfo]);

  useEffect(() => {
    if (fetchError) {
      setError(fetchError);
    }
  }, [fetchError]);

  useEffect(() => {
    if (sendError) {
      setError(sendError);
    }
  }, [sendError]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  return {
    message,
    setMessage,
    messages,
    error,
    users,
    handleSend,
    handleKeyPress,
    user,
    isSending,
    isLoading,
    messagesEndRef,
  };
};

export default useChatLogic;
