import React from "react";

const BottomNav = ({ user, onTribesClick, onChatsClick, onLoginClick }) => (
  <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 z-50 h-[100px]">
    <div className="h-full">
      {user ? (
        <div className="grid grid-cols-2 h-full">
          <button
            onClick={onTribesClick}
            className="bg-white text-black font-futura font-bold transition-colors hover:bg-gray-100 active:bg-gray-200 flex items-start pt-4 justify-center lowercase"
          >
            tribes
          </button>
          <button
            onClick={onChatsClick}
            className="bg-white text-black font-futura font-bold transition-colors hover:bg-gray-100 active:bg-gray-200 flex items-start pt-4 justify-center lowercase"
          >
            chats
          </button>
        </div>
      ) : (
        <button
          onClick={onLoginClick}
          className="w-full h-full bg-white text-black font-futura font-bold transition-colors hover:bg-gray-100 active:bg-gray-200 flex items-start pt-4 justify-center lowercase"
        >
          login
        </button>
      )}
    </div>
  </div>
);

export default BottomNav;
