import React from "react";
import { useAuthForm } from "../hooks/useAuthForm";
import SignUpFields from "./auth/SignUpFields";
import SignInFields from "./auth/SignInFields";
import SubmitButton from "./auth/SubmitButton";
import SwitchFormButton from "./auth/SwitchFormButton";

const AuthForm = ({ isSignUp: initialIsSignUp }) => {
  const {
    isSignUp,
    email,
    password,
    username,
    profilePicture,
    isLoading,
    countdown,
    error,
    message,
    handleSubmit,
    handleUsernameChange,
    handleFileChange,
    setIsSignUp,
    setEmail,
    setPassword,
  } = useAuthForm(initialIsSignUp);

  return (
    <form onSubmit={handleSubmit} className="space-y-4 lowercase">
      {isSignUp ? (
        <SignUpFields
          email={email}
          setEmail={setEmail}
          username={username}
          handleUsernameChange={handleUsernameChange}
          password={password}
          setPassword={setPassword}
          handleFileChange={handleFileChange}
        />
      ) : (
        <SignInFields
          username={username}
          handleUsernameChange={handleUsernameChange}
          password={password}
          setPassword={setPassword}
        />
      )}
      <SubmitButton
        isLoading={isLoading}
        countdown={countdown}
        isSignUp={isSignUp}
      />
      <SwitchFormButton isSignUp={isSignUp} setIsSignUp={setIsSignUp} />
      {error && <p className="text-red-500">{error.toLowerCase()}</p>}
      {message && <p className="text-green-500">{message.toLowerCase()}</p>}
    </form>
  );
};

export default AuthForm;
