import React, { useRef, useEffect } from "react";
import Modal from "./Modal";
import TribeSelector from "./TribeSelector";
import MessageForm from "./MessageForm";
import SignInPrompt from "./SignInPrompt";
import { useTribeModal } from "../hooks/useTribeModal";
import { usePreviewMessages } from "../hooks/usePreviewMessages";
import MessageDisplay from "./MessageDisplay";

const TribeModal = ({
  onClose,
  tribes,
  onSendMessage,
  isAuthenticated,
  moment,
  onSignInClick,
  onOpenChat,
  userTribe,
}) => {
  const modalRef = useRef();
  const {
    selectedTribe,
    setSelectedTribe,
    message,
    setMessage,
    showMessageForm,
    setShowMessageForm,
    error,
    contactedTribes,
    handleSendMessage,
    isSendDisabled,
    isSending,
  } = useTribeModal(tribes, moment, onSendMessage, onOpenChat, onClose);

  const { previewMessages } = usePreviewMessages(moment);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (modalRef.current && !modalRef.current.contains(event.target)) {
        onClose();
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [onClose]);

  return (
    <Modal onClose={onClose}>
      <div
        ref={modalRef}
        className="flex flex-col lowercase"
        onClick={(e) => e.stopPropagation()}
      >
        <h2 className="text-xl font-bold mb-4">
          {moment.tribe?.name.toLowerCase() || "unknown tribe"}.
        </h2>
        {userTribe && userTribe.id === moment.tribe?.id && (
          <p className="text-sm text-blue-500 mb-4">you are in this tribe</p>
        )}
        <MessageDisplay messages={previewMessages} />
        {isAuthenticated ? (
          <>
            {!showMessageForm && (
              <button
                onClick={() => setShowMessageForm(true)}
                className="w-full bg-zinc-950 text-white py-2 rounded-lg flex items-center justify-center"
              >
                send message
              </button>
            )}
            {showMessageForm && (
              <div className="space-y-4 mt-4">
                <TribeSelector
                  tribes={tribes}
                  selectedTribe={selectedTribe}
                  setSelectedTribe={setSelectedTribe}
                  contactedTribes={contactedTribes}
                  momentTribe={moment.tribe}
                />
                <MessageForm
                  message={message}
                  setMessage={setMessage}
                  onSend={handleSendMessage}
                  isSendDisabled={isSendDisabled}
                  isSending={isSending}
                />
              </div>
            )}
          </>
        ) : (
          <SignInPrompt
            onSignInClick={() => {
              onClose();
              onSignInClick();
            }}
          />
        )}
        {error && <div className="mt-4 text-red-500 text-center">{error}</div>}
      </div>
    </Modal>
  );
};

export default TribeModal;
