import { useEffect, useState } from "react";
import { supabase } from "../utils/supabaseClient";
import { useAuth } from "../contexts/AuthContext";
import { isValidLocation, formatLocation } from "../utils/locationValidation";

const useLocationUpdater = () => {
  const { user } = useAuth();
  const [error, setError] = useState(null);

  useEffect(() => {
    if (!user) return;

    const watchId = navigator.geolocation.watchPosition(
      async (position) => {
        const { latitude, longitude } = position.coords;
        try {
          if (!isValidLocation(latitude, longitude)) {
            throw new Error("Invalid location data received");
          }
          const formattedLocation = formatLocation(latitude, longitude);
          const { error } = await supabase
            .from("users")
            .update({
              last_known_location: formattedLocation,
              location_updated_at: new Date().toISOString(),
            })
            .eq("id", user.id);

          if (error) throw error;
        } catch (updateError) {
          console.error("Error updating location:", updateError);
          setError("Failed to update location. Some features may be limited.");
        }
      },
      (err) => {
        console.error("Error getting location:", err);
        setError("Failed to get your location. Some features may be limited.");
      },
      {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0,
      }
    );

    return () => {
      navigator.geolocation.clearWatch(watchId);
    };
  }, [user]);

  return { error };
};

export default useLocationUpdater;
