import React, { useState, useEffect } from "react";
import MainContent from "../components/MainContent";
import BottomNav from "../components/BottomNav";
import PanelManager from "../components/PanelManager";
import TribeModal from "../components/TribeModal";
import Chat from "../components/Chat";
import TribeToTribeChat from "../components/TribeToTribeChat";
import { useMoments } from "../hooks/useMoments";

const AppUI = ({
  user,
  tribes,
  tribeToTribeChats,
  showModal,
  showTribesPanel,
  showChatsPanel,
  showProfilePanel,
  selectedChat,
  selectedMoment,
  appError,
  onMomentClick,
  onClosePanels,
  onCreateTribe,
  onSendMessage,
  onAddMember,
  onLeaveTribe,
  onSignInClick,
  onOpenChat,
  getUserTribe,
  onApproveTribeInvitation,
  onDeclineTribeInvitation,
  setShowModal,
  setShowTribesPanel,
  setShowChatsPanel,
  setShowProfilePanel,
  setSelectedChat,
  setAppError,
  fetchData,
  userTribes,
  userLocation,
  requestLocation,
  locationStatus,
  isLocationEnabled,
  moments,
  momentsLoading,
  momentsError,
}) => {
  const [filterLocation, setFilterLocation] = useState(null);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const handleMomentClick = (moment) => {
    console.log("moment clicked in appui:", moment);
    onMomentClick(moment);
    setShowModal(true);
  };

  const handleLocationFilterChange = (newLocation) => {
    setFilterLocation(newLocation);
    fetchData();
  };

  const handleOpenChat = (chatInfo) => {
    setSelectedChat(chatInfo);
    setShowTribesPanel(false);
    setShowChatsPanel(false);
  };

  const isChatOpen = !!selectedChat;

  return (
    <div
      className={`min-h-screen bg-white text-black relative ${
        isChatOpen ? "overflow-hidden" : ""
      }`}
    >
      <MainContent
        moments={moments}
        loading={momentsLoading}
        error={momentsError}
        onMomentClick={handleMomentClick}
        fetchData={fetchData}
        userTribes={userTribes}
        userLocation={userLocation}
        locationStatus={locationStatus}
        requestLocation={requestLocation}
        onLocationFilterChange={handleLocationFilterChange}
        isChatOpen={isChatOpen}
        isLocationEnabled={isLocationEnabled}
      />
      {!isChatOpen && (
        <BottomNav
          user={user}
          onTribesClick={() => setShowTribesPanel(true)}
          onChatsClick={() => setShowChatsPanel(true)}
          onLoginClick={onSignInClick}
        />
      )}
      <PanelManager
        user={user}
        tribes={tribes}
        tribeToTribeChats={tribeToTribeChats}
        showTribesPanel={showTribesPanel}
        showChatsPanel={showChatsPanel}
        showProfilePanel={showProfilePanel}
        onClosePanels={onClosePanels}
        onCreateTribe={onCreateTribe}
        onSelectChat={handleOpenChat}
        onApproveTribeInvitation={onApproveTribeInvitation}
        onDeclineTribeInvitation={onDeclineTribeInvitation}
      />
      {showModal && selectedMoment && (
        <TribeModal
          onClose={() => setShowModal(false)}
          tribes={tribes}
          onSendMessage={onSendMessage}
          isAuthenticated={!!user}
          moment={selectedMoment}
          onSignInClick={onSignInClick}
          onOpenChat={handleOpenChat}
          userTribe={getUserTribe(selectedMoment?.tribe?.id)}
        />
      )}
      {selectedChat && selectedChat.type === "tribe" && (
        <Chat
          type={selectedChat.type}
          tribe={selectedChat.tribe}
          onClose={() => setSelectedChat(null)}
          onSendMessage={onSendMessage}
          onLeaveTribe={onLeaveTribe}
          onAddMember={onAddMember}
        />
      )}
      {selectedChat && selectedChat.type === "tribeToTribe" && (
        <TribeToTribeChat
          tribe={selectedChat.tribe}
          otherTribe={selectedChat.otherTribe}
          onClose={() => setSelectedChat(null)}
          onSendMessage={onSendMessage}
        />
      )}
    </div>
  );
};

export default AppUI;
