import { useState, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

export const usePreviewMessages = (moment) => {
  const [previewMessages, setPreviewMessages] = useState([]);

  useEffect(() => {
    const fetchPreviewMessages = async () => {
      if (moment && moment.tribe) {
        const { data, error } = await supabase
          .from("internal_messages")
          .select(
            "id, content, user_id, created_at, users(username), is_system_message"
          )
          .eq("tribe_id", moment.tribe.id)
          .order("created_at", { ascending: false })
          .limit(5);

        if (error) {
          console.error("Error fetching preview messages:", error);
        } else {
          setPreviewMessages(data.reverse());
        }
      }
    };

    fetchPreviewMessages();

    const subscription = supabase
      .channel(`tribe-messages-${moment?.tribe?.id}`)
      .on(
        "postgres_changes",
        {
          event: "INSERT",
          schema: "public",
          table: "internal_messages",
          filter: `tribe_id=eq.${moment?.tribe?.id}`,
        },
        async (payload) => {
          const { data: userData, error: userError } = await supabase
            .from("users")
            .select("username")
            .eq("id", payload.new.user_id)
            .single();

          if (userError) {
            console.error("Error fetching user data:", userError);
            return;
          }

          setPreviewMessages((currentMessages) => {
            const updatedMessages = [
              ...currentMessages,
              {
                ...payload.new,
                users: { username: userData.username || "Unknown User" },
              },
            ].slice(-5);
            return updatedMessages;
          });
        }
      )
      .subscribe();

    return () => {
      subscription.unsubscribe();
    };
  }, [moment]);

  return { previewMessages };
};
