import { parseLocation } from "./utils";

export const processTribes = (tribesData) => {
  return tribesData.map((tribe) => {
    const location = parseLocation(tribe.last_known_location);

    return {
      ...tribe,
      latitude: location ? location.latitude : null,
      longitude: location ? location.longitude : null,
      status: tribe.tribe_members[0]?.status || "unknown",
      hasUnreadMessages: tribe.tribe_members[0]?.has_unread_messages || false,
    };
  });
};
