import { supabase } from "../utils/supabaseClient";

export const useTribeMembers = (user) => {
  const handleLeaveTribe = async (tribeId) => {
    if (!user) throw new Error("User must be logged in to leave a tribe");
    if (!tribeId) throw new Error("Invalid tribe ID");

    try {
      const { error } = await supabase
        .from("tribe_members")
        .delete()
        .eq("tribe_id", tribeId)
        .eq("user_id", user.id);

      if (error) throw error;

      return true;
    } catch (error) {
      console.error("Error in handleLeaveTribe:", error);
      throw error;
    }
  };

  const handleAddMember = async (userId, tribeId) => {
    try {
      // Check if the current user is an admin of the tribe
      const { data: adminCheck, error: adminCheckError } = await supabase
        .from("tribe_members")
        .select("role")
        .eq("tribe_id", tribeId)
        .eq("user_id", user.id);

      if (adminCheckError) {
        console.error("Admin check error:", adminCheckError);
        throw new Error(`Admin check failed: ${adminCheckError.message}`);
      }

      if (
        !adminCheck ||
        adminCheck.length === 0 ||
        adminCheck[0].role !== "admin"
      ) {
        throw new Error("Only tribe admins can add new members");
      }

      const { data: newMember, error: memberError } = await supabase
        .from("tribe_members")
        .insert({
          tribe_id: tribeId,
          user_id: userId,
          role: "member",
          status: "active",
        })
        .single();

      if (memberError) {
        console.error("Member insertion error:", memberError);
        throw new Error(`Failed to add member: ${memberError.message}`);
      }

      return newMember;
    } catch (error) {
      console.error("Error in handleAddMember:", error);
      throw error;
    }
  };

  const handleApproveTribeInvitation = async (tribeId) => {
    if (!user)
      throw new Error("User must be logged in to approve a tribe invitation");

    try {
      const { error } = await supabase
        .from("tribe_members")
        .update({ status: "active" })
        .eq("tribe_id", tribeId)
        .eq("user_id", user.id)
        .eq("status", "invited");

      if (error) throw error;
    } catch (error) {
      console.error("Error in handleApproveTribeInvitation:", error);
      throw error;
    }
  };

  const handleDeclineTribeInvitation = async (tribeId) => {
    if (!user)
      throw new Error("User must be logged in to decline a tribe invitation");

    try {
      const { error } = await supabase
        .from("tribe_members")
        .delete()
        .eq("tribe_id", tribeId)
        .eq("user_id", user.id);

      if (error) throw error;
    } catch (error) {
      console.error("Error in handleDeclineTribeInvitation:", error);
      throw error;
    }
  };

  return {
    handleLeaveTribe,
    handleAddMember,
    handleApproveTribeInvitation,
    handleDeclineTribeInvitation,
  };
};
