import React, { useEffect, useState, useRef, useCallback } from "react";

const ChatsPanel = ({ user, tribeToTribeChats, onSelectChat }) => {
  const [displayedChats, setDisplayedChats] = useState([]);
  const [page, setPage] = useState(1);
  const loader = useRef(null);

  const loadMore = useCallback(() => {
    const newChats = tribeToTribeChats.slice(0, page * 10);
    setDisplayedChats(newChats);
  }, [tribeToTribeChats, page]);

  useEffect(() => {
    loadMore();
  }, [loadMore, page]);

  useEffect(() => {
    const options = {
      root: null,
      rootMargin: "20px",
      threshold: 1.0,
    };

    const observer = new IntersectionObserver((entities) => {
      const target = entities[0];
      if (target.isIntersecting) {
        setPage((prevPage) => prevPage + 1);
      }
    }, options);

    if (loader.current) {
      observer.observe(loader.current);
    }

    return () => {
      if (loader.current) {
        observer.unobserve(loader.current);
      }
    };
  }, []);

  if (!user) {
    return (
      <p className="text-sm text-gray-500 p-4 lowercase">
        please login to view chats.
      </p>
    );
  }

  return (
    <div className="h-full overflow-y-auto custom-scrollbar lowercase">
      {displayedChats.map((chat, index) => {
        const isUserTribe1 = chat.tribe1.created_by === user.id;
        const tribe1Name = chat.tribe1.name.toLowerCase();
        const tribe2Name = chat.tribe2.name.toLowerCase();

        return (
          <React.Fragment key={chat.id}>
            {index > 0 && <hr className="my-2 border-gray-200" />}
            <button
              onClick={() => {
                onSelectChat({
                  type: "tribeToTribe",
                  tribe: isUserTribe1 ? chat.tribe1 : chat.tribe2,
                  otherTribe: isUserTribe1 ? chat.tribe2 : chat.tribe1,
                  chatName: `${tribe1Name} ↔ ${tribe2Name}`,
                });
              }}
              className={`w-full text-left p-4 hover:bg-gray-100 rounded text-sm ${
                chat.hasUnreadMessages ? "font-bold" : ""
              }`}
            >
              <div className="flex flex-col items-center">
                <span>{tribe1Name}</span>
                <span className="text-gray-400">↓</span>
                <span>{tribe2Name}</span>
              </div>
            </button>
          </React.Fragment>
        );
      })}
      {displayedChats.length < tribeToTribeChats.length && (
        <div ref={loader} className="h-10" />
      )}
      {displayedChats.length === 0 && (
        <p className="text-sm text-gray-500 p-4">
          no chats yet. start a conversation with another tribe to begin!
        </p>
      )}
    </div>
  );
};

export default ChatsPanel;
