import { useState, useCallback } from "react";

export const useChatSend = (onSendMessage, tribe, otherTribe) => {
  const [message, setMessage] = useState("");
  const [isSending, setIsSending] = useState(false);
  const [error, setError] = useState(null);

  const handleSend = useCallback(async () => {
    if (!message.trim() || isSending) return;

    setIsSending(true);
    setError(null);

    try {
      await onSendMessage(tribe, message, otherTribe ? otherTribe : null);
      setMessage("");
    } catch (error) {
      console.error("Error sending message:", error);
      setError("Failed to send message. Please try again.");
    } finally {
      setIsSending(false);
    }
  }, [message, isSending, onSendMessage, tribe, otherTribe]);

  const handleKeyPress = useCallback(
    (e) => {
      if (e.key === "Enter" && !e.shiftKey) {
        e.preventDefault();
        handleSend();
      }
    },
    [handleSend]
  );

  return { message, setMessage, isSending, error, handleSend, handleKeyPress };
};
