import React from "react";

const Modal = ({ children, onClose }) => (
  <div className="fixed inset-0 bg-zinc-950 bg-opacity-50 flex justify-center items-center p-4 z-50">
    <div className="bg-white  rounded-lg max-w-md w-full max-h-[80vh] overflow-y-auto relative p-4">
      <button
        onClick={onClose}
        className="absolute top-2 right-2 text-gray-400"
      >
        ✕
      </button>
      {children}
    </div>
  </div>
);

export default Modal;
