import { useCallback } from "react";

export const useDistanceCalculation = () => {
  const calculateDistance = useCallback((lat1, lon1, lat2, lon2) => {
    if (!lat1 || !lon1 || !lat2 || !lon2) return Infinity;
    const R = 6371; // Radius of the Earth in km
    const dLat = ((lat2 - lat1) * Math.PI) / 180;
    const dLon = ((lon2 - lon1) * Math.PI) / 180;
    const a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos((lat1 * Math.PI) / 180) *
        Math.cos((lat2 * Math.PI) / 180) *
        Math.sin(dLon / 2) *
        Math.sin(dLon / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    const distance = R * c; // Distance in km
    return distance;
  }, []);

  const filterTribesByDistance = useCallback(
    (tribes, userLocation, radius) => {
      if (!userLocation) {
        return tribes;
      }

      return tribes.filter((tribe) => {
        if (!tribe.latitude || !tribe.longitude) {
          return false; // Exclude tribes without location
        }
        const distance = calculateDistance(
          userLocation.latitude,
          userLocation.longitude,
          tribe.latitude,
          tribe.longitude
        );
        return distance <= radius;
      });
    },
    [calculateDistance]
  );

  return { calculateDistance, filterTribesByDistance };
};
