import React from "react";

const TribeSelector = ({
  tribes,
  selectedTribe,
  setSelectedTribe,
  contactedTribes,
  momentTribe,
}) => {
  return (
    <select
      value={selectedTribe}
      onChange={(e) => setSelectedTribe(e.target.value)}
      className="w-full border rounded px-3 py-2"
    >
      <option value="">Select tribe...</option>
      {tribes.map((tribe) => (
        <option
          key={tribe.id}
          value={tribe.id}
          disabled={tribe.id === momentTribe?.id || contactedTribes[tribe.id]}
        >
          {tribe.name}
          {contactedTribes[tribe.id] ? " (Already messaged)" : ""}
        </option>
      ))}
    </select>
  );
};

export default TribeSelector;
