import React, { useState } from "react";

const LocationInfo = ({ userLocation, status, onRefresh }) => {
  const [isRefreshing, setIsRefreshing] = useState(false);

  const handleRefresh = async () => {
    setIsRefreshing(true);
    await onRefresh();
    setTimeout(() => setIsRefreshing(false), 2000);
  };

  const RefreshIcon = () => (
    <svg
      className="w-6 h-6 text-zinc-950"
      aria-hidden="true"
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      fill="none"
      viewBox="0 0 24 24"
    >
      <path
        stroke="currentColor"
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth="2"
        d="M17.651 7.65a7.131 7.131 0 0 0-12.68 3.15M18.001 4v4h-4m-7.652 8.35a7.13 7.13 0 0 0 12.68-3.15M6 20v-4h4"
      />
    </svg>
  );

  const HollowPinIcon = () => (
    <svg
      className="w-6 h-6 text-zinc-950"
      aria-hidden="true"
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      fill="none"
      viewBox="0 0 24 24"
    >
      <path
        stroke="currentColor"
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth="2"
        d="M12 15a6 6 0 1 0 0-12 6 6 0 0 0 0 12Zm0 0v6M9.5 9A2.5 2.5 0 0 1 12 6.5"
      />
    </svg>
  );

  const FilledPinIcon = () => (
    <svg
      className="w-6 h-6 text-zinc-950"
      aria-hidden="true"
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      fill="currentColor"
      viewBox="0 0 24 24"
    >
      <path
        fillRule="evenodd"
        d="M5 9a7 7 0 1 1 8 6.93V21a1 1 0 1 1-2 0v-5.07A7.001 7.001 0 0 1 5 9Zm5.94-1.06A1.5 1.5 0 0 1 12 7.5a1 1 0 1 0 0-2A3.5 3.5 0 0 0 8.5 9a1 1 0 0 0 2 0c0-.398.158-.78.44-1.06Z"
        clipRule="evenodd"
      />
    </svg>
  );

  return (
    <div className="fixed top-28 left-4 z-40">
      <button
        onClick={handleRefresh}
        className="font-futura font-bold flex items-center justify-center transition-transform active:scale-95 lowercase"
        disabled={isRefreshing}
      >
        {isRefreshing ? (
          <span className="animate-spin">
            <RefreshIcon />
          </span>
        ) : status === "granted" ? (
          <FilledPinIcon />
        ) : (
          <HollowPinIcon />
        )}
      </button>
    </div>
  );
};

export default LocationInfo;
