import React from "react";

const Header = ({ user, onProfileClick, isInChat }) => {
  return (
    <header className="bg-white text-black p-4 fixed top-0 left-0 right-0 z-10  flex justify-between items-center h-20">
      <h1 className="text-2xl font-bold lowercase">tribe.</h1>
      {user && !isInChat && (
        <button
          onClick={onProfileClick}
          className="bg-white text-zinc-950 font-futura font-bold rounded-full p-2 transition-transform active:scale-95 lowercase"
        >
          <svg
            className="w-6 h-6"
            aria-hidden="true"
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            fill="none"
            viewBox="0 0 24 24"
          >
            <path
              stroke="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d="M12 21a9 9 0 1 0 0-18 9 9 0 0 0 0 18Zm0 0a8.949 8.949 0 0 0 4.951-1.488A3.987 3.987 0 0 0 13 16h-2a3.987 3.987 0 0 0-3.951 3.512A8.948 8.948 0 0 0 12 21Zm3-11a3 3 0 1 1-6 0 3 3 0 0 1 6 0Z"
            />
          </svg>
        </button>
      )}
    </header>
  );
};

export default Header;
