import React, { useState, useEffect, useRef } from "react";
import MomentList from "./MomentList";
import PullToRefresh from "./PullToRefresh";
import LocationFilterButton from "./LocationFilterButton";
import LocationInfo from "./LocationInfo";

const MainContent = ({
  moments,
  loading,
  error,
  onMomentClick,
  fetchData,
  userTribes,
  userLocation,
  locationStatus,
  requestLocation,
  onLocationFilterChange,
  isChatOpen,
}) => {
  const [showDeviceSelect, setShowDeviceSelect] = useState(false);
  const [hideButtons, setHideButtons] = useState(false);
  const lastScrollY = useRef(0);

  const handleScroll = () => {};

  useEffect(() => {
    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const handleRefresh = async () => {
    console.log("refresh started");
    try {
      await fetchData();
      console.log("refresh completed successfully");
    } catch (error) {
      console.error("error during refresh:", error);
    }
  };

  const handleMomentClick = (moment) => {
    console.log("moment clicked in maincontent:", moment);
    onMomentClick(moment);
  };

  const isLocationEnabled = locationStatus === "granted";

  return (
    <PullToRefresh onRefresh={handleRefresh} isRefreshing={loading}>
      <main
        className={`p-4 pb-24 pt-32 relative ${
          isChatOpen ? "overflow-hidden" : ""
        }`}
      >
        <div
          className={`fixed top-24 left-4 right-4 flex justify-between items-center z-40 transition-opacity duration-300 ${
            hideButtons ? "opacity-0" : "opacity-100"
          }`}
        >
          <LocationInfo
            userLocation={userLocation}
            status={locationStatus}
            onRefresh={requestLocation}
          />
          <LocationFilterButton
            onLocationChange={onLocationFilterChange}
            isLocationEnabled={isLocationEnabled}
          />
        </div>
        <div className="relative z-10 mt-24">
          <MomentList
            moments={moments}
            loading={loading}
            error={error}
            onMomentClick={handleMomentClick}
            userTribes={userTribes}
            hasLocation={!!userLocation}
            isLocationEnabled={isLocationEnabled}
          />
        </div>
        {!isLocationEnabled && <div></div>}
      </main>
    </PullToRefresh>
  );
};

export default MainContent;
