import React from "react";
import MomentBubble from "./MomentBubble";

const MomentList = ({
  moments,
  loading,
  error,
  onMomentClick,
  userTribes,
  hasLocation,
  isLocationEnabled,
}) => {
  if (loading)
    return <p className="text-center py-4 lowercase">loading moments...</p>;
  if (error)
    return (
      <p className="text-center py-4 text-red-500 lowercase">error: {error}</p>
    );

  const sortedMoments = [...moments].sort(
    (a, b) => new Date(b.tribe.last_activity) - new Date(a.tribe.last_activity)
  );

  return (
    <div className="space-y-4">
      {sortedMoments.length === 0 ? (
        <p className="text-center py-4 lowercase">no moments to display</p>
      ) : (
        sortedMoments.map((moment, index) => (
          <div
            key={moment.id}
            className={
              !isLocationEnabled
                ? "opacity-90 filter blur-[2px] pointer-events-none"
                : ""
            }
          >
            <MomentBubble
              moment={moment}
              isLeft={index % 2 === 0}
              onMomentClick={isLocationEnabled ? onMomentClick : () => {}}
              isMember={userTribes.some(
                (tribe) => tribe.id === moment.tribe?.id
              )}
            />
          </div>
        ))
      )}
      {!isLocationEnabled && (
        <div className="fixed inset-0 bg-white bg-opacity-10 z-40 flex items-center justify-center pointer-events-none"></div>
      )}
    </div>
  );
};

export default MomentList;
