import React, { useState, useEffect, useRef } from "react";

const PullToRefresh = ({
  onRefresh,
  children,
  isRefreshing,
  isLocationRefresh = false,
}) => {
  const [startY, setStartY] = useState(0);
  const [pulling, setPulling] = useState(false);
  const containerRef = useRef(null);
  const pullThreshold = 100; // Adjust this value to change sensitivity
  const lastTouchEnd = useRef(0);

  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const handleTouchStart = (e) => {
      if (container.scrollTop === 0) {
        setStartY(e.touches[0].pageY);
        setPulling(true);
      }
    };

    const handleTouchMove = (e) => {
      if (!pulling) return;
      const y = e.touches[0].pageY;
      const pullDistance = y - startY;
      if (pullDistance > 0 && pullDistance < 200) {
        container.style.transform = `translateY(${pullDistance / 2}px)`;
      }
    };

    const handleTouchEnd = async (e) => {
      if (!pulling) return;
      setPulling(false);
      container.style.transform = "translateY(0)";

      const now = Date.now();
      const timeSinceLastTouch = now - lastTouchEnd.current;

      if (
        container.scrollTop === 0 &&
        startY - e.changedTouches[0].pageY < -pullThreshold &&
        timeSinceLastTouch > 300 // Prevent rapid consecutive refreshes
      ) {
        await onRefresh();
        lastTouchEnd.current = now;
      }
    };

    container.addEventListener("touchstart", handleTouchStart, {
      passive: true,
    });
    container.addEventListener("touchmove", handleTouchMove, { passive: true });
    container.addEventListener("touchend", handleTouchEnd);

    return () => {
      container.removeEventListener("touchstart", handleTouchStart);
      container.removeEventListener("touchmove", handleTouchMove);
      container.removeEventListener("touchend", handleTouchEnd);
    };
  }, [pulling, startY, onRefresh]);

  return (
    <div ref={containerRef} className="relative h-full overflow-y-auto">
      {isRefreshing && !isLocationRefresh && (
        <div className="absolute top-0 left-0 right-0 bg-zinc-950 text-white text-center py-2">
          Refreshing...
        </div>
      )}
      {children}
    </div>
  );
};

export default PullToRefresh;
