import React from "react";

const SubmitButton = ({ isLoading, countdown, isSignUp }) => {
  return (
    <button
      type="submit"
      disabled={isLoading || countdown > 0}
      className={`w-full px-4 py-2 bg-zinc-950 text-white font-futura font-bold text-white rounded-md ${
        isLoading || countdown > 0
          ? "opacity-50 cursor-not-allowed"
          : "hover:bg-zinc-950 text-white font-futura font-bold text-white"
      }`}
    >
      {isLoading
        ? "Processing..."
        : countdown > 0
        ? `Try again in ${countdown}s`
        : isSignUp
        ? "sign up"
        : "login"}
    </button>
  );
};

export default SubmitButton;
