import React from "react";

const getColorForUser = (userId, userColorMap) => {
  const colors = [
    "bg-[#4749fe]",
    "bg-[#ee285b]",
    "bg-[#ffd700]",
    "bg-green-100",
    "bg-purple-100",
  ];
  if (!userColorMap.has(userId)) {
    userColorMap.set(userId, colors[userColorMap.size % colors.length]);
  }
  return userColorMap.get(userId);
};

const formatTimeAgo = (dateString) => {
  const now = new Date();
  const date = new Date(dateString);
  const diffInMinutes = Math.floor((now - date) / (1000 * 60));

  if (diffInMinutes < 60) {
    return `${diffInMinutes} minute${diffInMinutes !== 1 ? "s" : ""} ago`;
  } else if (diffInMinutes < 24 * 60) {
    const diffInHours = Math.floor(diffInMinutes / 60);
    return `${diffInHours} hour${diffInHours !== 1 ? "s" : ""} ago`;
  } else {
    const diffInDays = Math.floor(diffInMinutes / (24 * 60));
    return `${diffInDays} day${diffInDays !== 1 ? "s" : ""} ago`;
  }
};

const MomentBubble = ({ moment, isLeft, onMomentClick, isMember }) => {
  if (!moment || !moment.recent_messages) return null;

  const userColorMap = new Map();

  const distanceText =
    moment.distance !== null
      ? `${moment.distance.toFixed(1)}km away`
      : "location unknown";

  const handleClick = () => {
    console.log("momentbubble clicked:", moment);
    if (typeof onMomentClick === "function") {
      onMomentClick(moment);
    } else {
      console.error("onmomentclick is not a function", onMomentClick);
    }
  };

  return (
    <div className={`flex ${isLeft ? "justify-start" : "justify-end"} mb-4`}>
      <div
        className={`moment-bubble max-w-[70%] ${
          isLeft ? "ml-2" : "mr-2"
        } bg-white border border-zinc-950 rounded-lg p-3 shadow-sm cursor-pointer transition-all duration-200 hover:shadow-md active:shadow-inner lowercase relative`}
        onClick={handleClick}
      >
        <h3 className="font-bold mb-2 text-zinc-950 font-futura">
          {moment.tribe?.name.toLowerCase()}.
        </h3>
        <div className="w-full  rounded-md mb-2 overflow-hidden">
          <div className="p-2 text-white text-xs">
            {moment.recent_messages.map((message) =>
              message.is_system_message ? (
                <div key={message.id} className="text-gray-600 italic mb-2">
                  {message.content.toLowerCase()}
                </div>
              ) : (
                <div
                  key={message.id}
                  className={`rounded p-2 mb-2 ${getColorForUser(
                    message.user_id,
                    userColorMap
                  )} text-left`}
                >
                  <p className="font-bold">{message.username.toLowerCase()}:</p>
                  <p>{message.content.toLowerCase()}</p>
                </div>
              )
            )}
          </div>
        </div>
        <p className="text-xs text-gray-500">
          {moment.tribe?.last_activity
            ? formatTimeAgo(moment.tribe.last_activity)
            : "no date"}
        </p>
        <p className="text-xs text-gray-400 mt-1">{distanceText}</p>
        {isMember && (
          <div
            className="absolute bottom-2 right-2 text-black"
            title="you are a member of this tribe"
          >
            <svg
              className="w-6 h-6"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeWidth="2"
                d="M4.5 17H4a1 1 0 0 1-1-1 3 3 0 0 1 3-3h1m0-3.05A2.5 2.5 0 1 1 9 5.5M19.5 17h.5a1 1 0 0 0 1-1 3 3 0 0 0-3-3h-1m0-3.05a2.5 2.5 0 1 0-2-4.45m.5 13.5h-7a1 1 0 0 1-1-1 3 3 0 0 1 3-3h3a3 3 0 0 1 3 3 1 1 0 0 1-1 1Zm-1-9.5a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0Z"
              />
            </svg>
          </div>
        )}
      </div>
    </div>
  );
};

export default MomentBubble;
