// src/utils/locationValidation.js

export const isValidLocation = (latitude, longitude) => {
  const isValidLatitude = latitude >= -90 && latitude <= 90;
  const isValidLongitude = longitude >= -180 && longitude <= 180;
  return isValidLatitude && isValidLongitude;
};

export const formatLocation = (latitude, longitude) => {
  if (!isValidLocation(latitude, longitude)) {
    throw new Error("Invalid latitude or longitude");
  }
  return `POINT(${longitude} ${latitude})`;
};
