import { useState, useCallback, useEffect } from "react";
import { fetchTribes, createTribe } from "../api/tribeApi";
import { useTribeMembers } from "./useTribeMembers";
import { useTribeMessages } from "./useTribeMessages";
import { processTribes } from "../utils/tribeUtils";
import { supabase } from "../utils/supabaseClient";
import { parseLocation } from "../utils/utils";

export const useTribes = (user) => {
  const [tribes, setTribes] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const {
    handleLeaveTribe,
    handleAddMember,
    handleApproveTribeInvitation,
    handleDeclineTribeInvitation,
  } = useTribeMembers(user);
  const { handleCreateTribeMessage } = useTribeMessages(user);

  const fetchTribesData = useCallback(async () => {
    if (!user) return;
    setLoading(true);
    setError(null);
    try {
      const tribesData = await fetchTribes(user.id);
      console.log("raw tribes data:", tribesData);
      const processedTribes = processTribes(tribesData);
      console.log("processed tribes:", processedTribes);
      setTribes(processedTribes);
    } catch (error) {
      console.error("error fetching tribes:", error);
      setError("failed to fetch tribes: " + error.message);
    } finally {
      setLoading(false);
    }
  }, [user]);

  useEffect(() => {
    fetchTribesData();
  }, [fetchTribesData]);

  const updateTribeLocation = async (
    tribeId,
    userLocation,
    userLocationUpdatedAt
  ) => {
    try {
      const { data: tribeData, error: tribeError } = await supabase
        .from("tribes")
        .select("last_known_location, location_updated_at")
        .eq("id", tribeId)
        .single();

      if (tribeError) throw tribeError;

      if (
        !tribeData.location_updated_at ||
        new Date(userLocationUpdatedAt) >
          new Date(tribeData.location_updated_at)
      ) {
        const { error: updateError } = await supabase
          .from("tribes")
          .update({
            last_known_location: userLocation,
            location_updated_at: userLocationUpdatedAt,
          })
          .eq("id", tribeId);

        if (updateError) throw updateError;
      }
    } catch (error) {
      console.error("error updating tribe location:", error);
    }
  };

  const handleCreateTribe = async (tribeName) => {
    if (!user) throw new Error("user must be logged in to create a tribe");
    if (!tribeName.trim()) throw new Error("tribe name cannot be empty");

    try {
      const { data: userData, error: userError } = await supabase
        .from("users")
        .select("last_known_location, location_updated_at")
        .eq("id", user.id)
        .single();

      if (userError) throw userError;

      const newTribe = await createTribe(user.id, tribeName.trim());

      if (userData.last_known_location) {
        await updateTribeLocation(
          newTribe.id,
          userData.last_known_location,
          userData.location_updated_at
        );
      }

      await supabase.from("tribe_members").insert({
        tribe_id: newTribe.id,
        user_id: user.id,
        role: "admin",
        status: "active",
      });

      await handleCreateTribeMessage(
        newTribe.id,
        `tribe "${tribeName}" created by ${user.username}`,
        true
      );
      await fetchTribesData();
      return newTribe;
    } catch (error) {
      console.error("error in handleCreateTribe:", error);
      throw error;
    }
  };

  const handleSendMessage = async (tribeId, message) => {
    try {
      await handleCreateTribeMessage(tribeId, message);

      const { data: userData, error: userError } = await supabase
        .from("users")
        .select("last_known_location, location_updated_at")
        .eq("id", user.id)
        .single();

      if (userError) throw userError;

      if (userData.last_known_location) {
        await updateTribeLocation(
          tribeId,
          userData.last_known_location,
          userData.location_updated_at
        );
      }

      await fetchTribesData();
    } catch (error) {
      console.error("error in handleSendMessage:", error);
      throw error;
    }
  };

  const handleInviteMember = async (tribeId, invitedUserId) => {
    try {
      await handleAddMember(tribeId, invitedUserId);

      // Fetch the invited user's username
      const { data: invitedUserData, error: invitedUserError } = await supabase
        .from("users")
        .select("username")
        .eq("id", invitedUserId)
        .single();

      if (invitedUserError) throw invitedUserError;

      // Create system message for the invitation
      await handleCreateTribeMessage(
        tribeId,
        `@${invitedUserData.username} has been invited to the tribe`,
        true
      );

      await fetchTribesData();
    } catch (error) {
      console.error("error in handleInviteMember:", error);
      throw error;
    }
  };

  return {
    tribes,
    loading,
    error,
    fetchTribes: fetchTribesData,
    handleCreateTribe,
    handleLeaveTribe,
    handleAddMember: handleInviteMember,
    handleApproveTribeInvitation,
    handleDeclineTribeInvitation,
    handleSendMessage,
  };
};
