import { supabase } from "../utils/supabaseClient";

export const useSignOut = (setUser) => {
  const signOut = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      setUser(null);
    } catch (error) {
      console.error("Error during sign out:", error);
      throw error;
    }
  };

  return { signOut };
};
