import { useState, useCallback, useEffect } from "react";
import { useAuth } from "../contexts/AuthContext";
import { supabase } from "../utils/supabaseClient";

export const useAuthForm = (initialIsSignUp) => {
  const [isSignUp, setIsSignUp] = useState(initialIsSignUp);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [username, setUsername] = useState("");
  const [profilePicture, setProfilePicture] = useState(null);
  const [error, setError] = useState("");
  const [message, setMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [countdown, setCountdown] = useState(0);
  const { signIn, signUp } = useAuth();

  const startCountdown = useCallback((seconds) => {
    setCountdown(seconds);
    const timer = setInterval(() => {
      setCountdown((prevCount) => {
        if (prevCount <= 1) {
          clearInterval(timer);
          return 0;
        }
        return prevCount - 1;
      });
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    if (countdown === 0) {
      setError("");
    }
  }, [countdown]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");
    setMessage("");
    setIsLoading(true);
    try {
      const cleanUsername = username.startsWith("@")
        ? username.slice(1)
        : username;

      if (isSignUp) {
        let profilePictureUrl = null;
        if (profilePicture) {
          profilePictureUrl = await uploadProfilePicture(profilePicture);
        }

        const { user, error, message } = await signUp({
          email,
          password,
          username: cleanUsername,
          profile_picture: profilePictureUrl,
        });
        if (error) {
          throw new Error(error);
        }
        setMessage(
          message ||
            "Sign up successful! Please check your email for verification."
        );
      } else {
        const { user, error } = await signIn({
          username: cleanUsername,
          password,
        });
        if (error) throw error;
        setMessage("Sign in successful!");
      }
    } catch (error) {
      console.error("Error in AuthForm:", error);
      setError(error.message || "An error occurred during authentication");
      if (
        error.message &&
        error.message.includes &&
        error.message.includes("Please wait")
      ) {
        const seconds = parseInt(error.message.match(/\d+/)[0], 10);
        startCountdown(seconds);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleUsernameChange = (e) => {
    const value = e.target.value;
    setUsername(value.startsWith("@") ? value : `@${value}`);
  };

  const handleFileChange = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      setProfilePicture(e.target.files[0]);
    }
  };

  const uploadProfilePicture = async (file) => {
    const fileExt = file.name.split(".").pop();
    const fileName = `${Math.random().toString(36).substring(2)}.${fileExt}`;
    const { data, error: uploadError } = await supabase.storage
      .from("profile_pictures")
      .upload(fileName, file, {
        cacheControl: "3600",
        upsert: false,
      });

    if (uploadError) {
      console.error("Error uploading file:", uploadError);
      throw new Error("Failed to upload profile picture. Please try again.");
    }

    const { data: urlData, error: urlError } = supabase.storage
      .from("profile_pictures")
      .getPublicUrl(fileName);

    if (urlError) {
      console.error("Error getting public URL:", urlError);
      throw new Error("Failed to get profile picture URL. Please try again.");
    }

    return urlData.publicUrl;
  };

  return {
    isSignUp,
    setIsSignUp,
    email,
    setEmail,
    password,
    setPassword,
    username,
    profilePicture,
    isLoading,
    countdown,
    error,
    message,
    handleSubmit,
    handleUsernameChange,
    handleFileChange,
  };
};
