import React from "react";

const MessageInput = ({
  message,
  setMessage,
  handleSend,
  handleKeyPress,
  isSending,
}) => {
  const onSendClick = (e) => {
    e.preventDefault();
    console.log("Send button clicked");
    handleSend();
  };

  return (
    <form onSubmit={onSendClick} className="p-4 border-t flex">
      <input
        type="text"
        value={message}
        onChange={(e) => setMessage(e.target.value)}
        onKeyPress={handleKeyPress}
        className="flex-1 border rounded-l-lg px-3 py-2"
        placeholder="Type a message..."
      />
      <button
        type="submit"
        disabled={isSending}
        className={`bg-zinc-950 text-white px-4 py-2 rounded-r-lg ${
          isSending ? "opacity-50 cursor-not-allowed" : ""
        }`}
      >
        {isSending ? "Sending..." : "Send"}
      </button>
    </form>
  );
};

export default MessageInput;
