import React, { useState } from "react";
import AuthForm from "./AuthForm";
import { useAuth } from "../contexts/AuthContext";

const SidePanel = ({ isOpen, onClose, title, showSignIn = false, content }) => {
  const [isSignUp, setIsSignUp] = useState(false);
  const { user, signOut, updateUserProfileImage } = useAuth();
  const [isUpdating, setIsUpdating] = useState(false);
  const [updateError, setUpdateError] = useState(null);

  const handleFileChange = async (event) => {
    const file = event.target.files[0];
    if (file) {
      setIsUpdating(true);
      setUpdateError(null);
      try {
        await updateUserProfileImage(file);
      } catch (error) {
        setUpdateError("Failed to update profile picture. Please try again.");
      } finally {
        setIsUpdating(false);
      }
    }
  };

  return (
    <>
      {isOpen && (
        <div
          className="fixed inset-0 bg-zinc-950 bg-opacity-50 z-50"
          onClick={onClose}
        ></div>
      )}
      <div
        className={`fixed inset-y-0 left-0 w-64 bg-white transform ${
          isOpen ? "translate-x-0" : "-translate-x-full"
        } transition-transform duration-300 ease-in-out z-50 flex flex-col pt-20`}
      >
        <div className="p-3 border-b flex justify-between items-center">
          <h2 className="text-lg font-bold truncate">{title}</h2>
          <button onClick={onClose} className="text-black">
            <svg
              className="w-6 h-6"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M6 18 17.94 6M18 18 6.06 6"
              />
            </svg>
          </button>
        </div>
        <div className="p-3 overflow-y-auto flex-grow">
          {content ? (
            content
          ) : user ? (
            <div className="space-y-4">
              <div className="w-48 h-48 bg-gray-200 rounded-lg mx-auto flex items-center justify-center relative">
                <img
                  src={
                    user.profile_picture || "https://via.placeholder.com/150"
                  }
                  alt={user.username}
                  className="w-full h-full object-cover rounded-lg"
                />
                <label
                  htmlFor="profile-picture-upload"
                  className="absolute bottom-2 right-2 bg-black text-white font-futura font-bold text-white p-2 rounded-full cursor-pointer"
                >
                  📷
                </label>
                <input
                  id="profile-picture-upload"
                  type="file"
                  accept="image/*"
                  onChange={handleFileChange}
                  className="hidden"
                />
              </div>
              {isUpdating && (
                <p className="text-blue-500 text-center">
                  Updating profile picture...
                </p>
              )}
              {updateError && (
                <p className="text-red-500 text-center">{updateError}</p>
              )}
              <p className="text-center font-bold">{user.username}</p>
              <button
                onClick={signOut}
                className="bg-black text-white font-futura font-bold w-full btn"
              >
                sign out
              </button>
            </div>
          ) : showSignIn ? (
            <AuthForm
              isSignUp={isSignUp}
              onToggle={() => setIsSignUp(!isSignUp)}
            />
          ) : (
            <div className="flex flex-col space-y-4">
              <button
                onClick={() => {
                  setIsSignUp(false);
                }}
                className="bg-black text-white font-futura font-bold w-full btn"
              >
                login
              </button>
              <button
                onClick={() => {
                  setIsSignUp(true);
                }}
                className="bg-black text-white font-futura font-bold w-full btn bg-white text-gray-900 border border-gray-900"
              >
                sign up
              </button>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default SidePanel;
