export const parseEWKT = (ewkt) => {
  const match = ewkt.match(/SRID=(\d+);POINT\(([^ ]+) ([^)]+)\)/);
  if (match) {
    return {
      srid: parseInt(match[1]),
      longitude: parseFloat(match[2]),
      latitude: parseFloat(match[3]),
    };
  }
  return null;
};

export const parseBinaryLocation = (binaryString) => {
  const bytes = new Uint8Array(binaryString.length / 2);
  for (let i = 0; i < bytes.length; i++) {
    bytes[i] = parseInt(binaryString.substr(i * 2, 2), 16);
  }
  const view = new DataView(bytes.buffer);

  const srid = view.getInt32(0, true);
  const longitude = view.getFloat64(4, true);
  const latitude = view.getFloat64(12, true);

  return { srid, longitude, latitude };
};
