import { useState, useCallback } from "react";
import { supabase } from "../utils/supabaseClient";
import { calculateDistance } from "../utils/utils";
import { parseEWKT, parseBinaryLocation } from "./useLocationParsing";
import { useRealtimeMessages } from "./useRealtimeMessages";

export const useMoments = () => {
  const [moments, setMoments] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchMoments = useCallback(
    async (userLocation = null, filterLocation = null) => {
      console.log("Fetching moments");
      setLoading(true);
      setError(null);
      try {
        const { data: momentsData, error: momentsError } = await supabase
          .from("moments")
          .select(
            `
            id, created_at, tribe_id,
            tribe:tribes(id, name, last_known_location, location_updated_at, last_activity)
          `
          )
          .order("created_at", { ascending: false });

        if (momentsError) throw momentsError;

        console.log("Fetched raw moments data:", momentsData);

        if (!momentsData || momentsData.length === 0) {
          console.log("No moments data returned from the database");
          setMoments([]);
          return;
        }

        const { data: messagesData, error: messagesError } = await supabase
          .from("internal_messages")
          .select(
            `
            id, content, user_id, created_at, tribe_id, is_system_message,
            users(username)
          `
          )
          .in(
            "tribe_id",
            momentsData.map((m) => m.tribe_id)
          )
          .order("created_at", { ascending: false });

        if (messagesError) throw messagesError;

        const momentsWithMessages = momentsData.map((moment) => {
          let distance = null;
          if (
            moment.tribe &&
            moment.tribe.last_known_location &&
            userLocation
          ) {
            try {
              let tribeLocation = moment.tribe.last_known_location;

              if (typeof tribeLocation === "string") {
                if (tribeLocation.startsWith("0101000020")) {
                  tribeLocation = parseBinaryLocation(tribeLocation);
                } else {
                  const parsedLocation = parseEWKT(tribeLocation);
                  if (parsedLocation) {
                    tribeLocation = parsedLocation;
                  } else {
                    console.error(
                      "Invalid tribe location format:",
                      tribeLocation
                    );
                    return { ...moment, distance: null };
                  }
                }
              }

              console.log("Parsed tribe location:", tribeLocation);

              if (
                tribeLocation &&
                "latitude" in tribeLocation &&
                "longitude" in tribeLocation &&
                userLocation
              ) {
                distance = calculateDistance(
                  userLocation.latitude,
                  userLocation.longitude,
                  tribeLocation.latitude,
                  tribeLocation.longitude
                );
                console.log("Calculated distance:", distance);
              } else {
                console.log("Unable to calculate distance due to missing data");
              }
            } catch (error) {
              console.error("Error parsing tribe location:", error);
            }
          } else {
            console.log(
              "Tribe or tribe location not available for moment:",
              moment.id
            );
          }

          const tribeMessages = messagesData
            .filter((msg) => msg.tribe_id === moment.tribe_id)
            .slice(0, 3);

          return {
            ...moment,
            distance,
            recent_messages: tribeMessages.map((msg) => ({
              ...msg,
              username: msg.users?.username || "Unknown User",
            })),
          };
        });

        console.log("Moments with messages:", momentsWithMessages);

        let filteredMoments = momentsWithMessages;

        if (filterLocation) {
          filteredMoments = momentsWithMessages.filter((moment) => {
            if (!moment.tribe || !moment.tribe.last_known_location)
              return false;
            const momentLocation = parseEWKT(moment.tribe.last_known_location);
            if (!momentLocation) return false;
            const distanceToFilter = calculateDistance(
              filterLocation.latitude,
              filterLocation.longitude,
              momentLocation.latitude,
              momentLocation.longitude
            );
            // Filter moments within 50km of the filter location
            return distanceToFilter <= 50;
          });
        }

        const sortedMoments = filteredMoments.sort((a, b) => {
          if (a.distance !== null && b.distance !== null) {
            return a.distance - b.distance;
          } else if (a.distance !== null) {
            return -1;
          } else if (b.distance !== null) {
            return 1;
          } else {
            return (
              new Date(b.tribe.last_activity) - new Date(a.tribe.last_activity)
            );
          }
        });

        // Limit to 18 moments
        const limitedMoments = sortedMoments.slice(0, 18);

        console.log("Limited moments:", limitedMoments);

        setMoments(limitedMoments);
      } catch (error) {
        console.error("Error fetching moments:", error);
        setError("Failed to fetch moments: " + error.message);
      } finally {
        setLoading(false);
      }
    },
    []
  );

  useRealtimeMessages(setMoments);

  return { moments, loading, error, fetchMoments };
};
