import { useCallback } from "react";
import { supabase } from "../utils/supabaseClient";

export const useMessages = (user) => {
  const handleSendMessage = useCallback(
    async (tribe, message, targetTribe = null) => {
      if (!user) throw new Error("User must be logged in to send a message");
      if (!tribe || !tribe.id) throw new Error("Invalid tribe");

      try {
        let newMessage;
        if (targetTribe) {
          let chatId;
          const { data: chatData, error: chatError } = await supabase
            .from("tribe_to_tribe_chats")
            .select("id")
            .or(
              `and(tribe1_id.eq.${tribe.id},tribe2_id.eq.${targetTribe.id}),and(tribe1_id.eq.${targetTribe.id},tribe2_id.eq.${tribe.id})`
            );

          if (chatError) throw chatError;

          if (chatData && chatData.length > 0) {
            chatId = chatData[0].id;
          } else {
            // Create a new chat if one doesn't exist
            const { data: newChat, error: newChatError } = await supabase
              .from("tribe_to_tribe_chats")
              .insert({
                tribe1_id: tribe.id,
                tribe2_id: targetTribe.id,
              })
              .select()
              .single();

            if (newChatError) throw newChatError;
            chatId = newChat.id;
          }

          const { data, error } = await supabase
            .from("tribe_to_tribe_messages")
            .insert({
              content: message,
              chat_id: chatId,
              tribe_id: tribe.id,
              user_id: user.id,
            })
            .select()
            .single();

          if (error) throw error;
          newMessage = data;
        } else {
          const { data, error } = await supabase
            .from("internal_messages")
            .insert({
              content: message,
              tribe_id: tribe.id,
              user_id: user.id,
            })
            .select()
            .single();

          if (error) throw error;
          newMessage = data;
        }

        return newMessage;
      } catch (error) {
        console.error("Error in handleSendMessage:", error);
        throw error;
      }
    },
    [user]
  );

  return { handleSendMessage };
};
