import React from "react";

const MembersList = ({ members, onClose }) => {
  return (
    <div className="fixed inset-0 bg-black950 bg-opacity-50 flex justify-center items-center">
      <div className="bg-white p-6 rounded-lg w-80">
        <h3 className="text-lg font-bold mb-4">Tribe Members</h3>
        <ul className="list-disc pl-5">
          {members.map((member) => (
            <li key={member.id}>{member.username}</li>
          ))}
        </ul>
        <button
          onClick={onClose}
          className="mt-4 bg-black950 text-white px-4 py-2 rounded"
        >
          Close
        </button>
      </div>
    </div>
  );
};

export default MembersList;
