import React, { useState, useEffect, useRef } from "react";
import { debounce } from "lodash";

const ManualLocationInput = ({ onSubmit, onCancel }) => {
  const [input, setInput] = useState("");
  const [suggestions, setSuggestions] = useState([]);
  const [selectedLocation, setSelectedLocation] = useState(null);
  const autocompleteRef = useRef(null);

  useEffect(() => {
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const handleClickOutside = (event) => {
    if (
      autocompleteRef.current &&
      !autocompleteRef.current.contains(event.target)
    ) {
      setSuggestions([]);
    }
  };

  const fetchSuggestions = debounce(async (input) => {
    if (input.length < 3) return;
    try {
      const response = await fetch(
        `https://nominatim.openstreetmap.org/search?format=json&q=${input}&addressdetails=1`
      );
      const data = await response.json();
      const uniqueCities = Array.from(
        new Set(
          data.map((item) => ({
            display_name: `${
              item.address.city ||
              item.address.town ||
              item.address.village ||
              item.name
            }, ${item.address.country}`,
            lat: item.lat,
            lon: item.lon,
          }))
        )
      );
      setSuggestions(uniqueCities.slice(0, 5));
    } catch (error) {
      console.error("error fetching suggestions:", error);
    }
  }, 300);

  const handleInputChange = (e) => {
    const value = e.target.value.toLowerCase();
    setInput(value);
    fetchSuggestions(value);
  };

  const handleSuggestionClick = (suggestion) => {
    setInput(suggestion.display_name.toLowerCase());
    setSelectedLocation(suggestion);
    setSuggestions([]);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (selectedLocation) {
      onSubmit(
        selectedLocation.display_name.toLowerCase(),
        selectedLocation.lat,
        selectedLocation.lon
      );
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4 lowercase">
      <div ref={autocompleteRef} className="relative">
        <label
          htmlFor="location"
          className="block text-sm font-medium text-gray-700"
        >
          city, country
        </label>
        <input
          type="text"
          id="location"
          value={input}
          onChange={handleInputChange}
          required
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-300 focus:ring focus:ring-blue-200 focus:ring-opacity-50"
          placeholder="enter city and country"
        />
        {suggestions.length > 0 && (
          <ul className="absolute z-10 w-full bg-white border border-gray-300 rounded-md shadow-lg mt-1">
            {suggestions.map((suggestion, index) => (
              <li
                key={index}
                onClick={() => handleSuggestionClick(suggestion)}
                className="px-4 py-2 hover:bg-gray-100 cursor-pointer"
              >
                {suggestion.display_name.toLowerCase()}
              </li>
            ))}
          </ul>
        )}
      </div>
      <div className="flex justify-between">
        <button
          type="submit"
          className="inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-zinc-950 hover:bg-zinc-950 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-transform active:scale-95"
        >
          submit
        </button>
        <button
          type="button"
          onClick={onCancel}
          className="inline-flex justify-center py-2 px-4 border border-blue-300 shadow-sm text-sm font-medium rounded-md text-blue-700 bg-white hover:bg-zinc-950 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-transform active:scale-95"
        >
          cancel
        </button>
      </div>
    </form>
  );
};

export default ManualLocationInput;
