import { useState, useEffect, useCallback } from "react";
import { useAuth } from "../contexts/AuthContext";
import { useTribes } from "./useTribes";
import { useMoments } from "./useMoments";
import { useTribeToTribeChats } from "./useTribeToTribeChats";
import { useMessages } from "./useMessages";
import { supabase } from "../utils/supabaseClient";

export const useAppData = () => {
  const { user } = useAuth();
  const {
    tribes,
    fetchTribes,
    handleCreateTribe,
    handleLeaveTribe,
    handleAddMember,
    handleApproveTribeInvitation,
    handleDeclineTribeInvitation,
  } = useTribes(user);
  const { moments, fetchMoments } = useMoments();
  const { tribeToTribeChats, fetchTribeToTribeChats } = useTribeToTribeChats(
    user,
    tribes
  );
  const { handleSendMessage } = useMessages(user);

  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [lastFetchTime, setLastFetchTime] = useState(0);

  const fetchData = useCallback(async () => {
    const now = Date.now();
    if (now - lastFetchTime < 5000) {
      console.log("Skipping fetch, last fetch was too recent");
      return;
    }

    setLoading(true);
    setError(null);
    try {
      await fetchMoments();
      if (user) {
        await Promise.all([fetchTribes(), fetchTribeToTribeChats()]);
      }
    } catch (error) {
      console.error("Error fetching data:", error);
      setError("Failed to fetch data. Please try again.");
    } finally {
      setLoading(false);
      setLastFetchTime(now);
    }
  }, [user, lastFetchTime, fetchTribes, fetchMoments, fetchTribeToTribeChats]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  useEffect(() => {
    if (user) {
      const channel = supabase
        .channel("realtime app")
        .on(
          "postgres_changes",
          {
            event: "INSERT",
            schema: "public",
            table: "tribe_members",
            filter: `user_id=eq.${user.id} AND status=eq.invited`,
          },
          () => {
            fetchData();
          }
        )
        .on(
          "postgres_changes",
          { event: "INSERT", schema: "public", table: "tribe_to_tribe_chats" },
          () => {
            fetchData();
          }
        )
        .on(
          "postgres_changes",
          {
            event: "UPDATE",
            schema: "public",
            table: "chat_participants",
            filter: `user_id=eq.${user.id} AND has_unread_messages=eq.true`,
          },
          () => {
            fetchData();
          }
        )
        .subscribe();

      return () => {
        supabase.removeChannel(channel);
      };
    }
  }, [user, fetchData]);

  return {
    tribes,
    moments,
    tribeToTribeChats,
    loading,
    error,
    fetchData,
    handleCreateTribe,
    handleSendMessage,
    handleAddMember,
    handleLeaveTribe,
    handleApproveTribeInvitation,
    handleDeclineTribeInvitation,
  };
};

export default useAppData;
