import React, { useState, useEffect, useRef } from "react";
import ManualLocationInput from "./ManualLocationInput";

const LocationFilterButton = ({ onLocationChange, isLocationEnabled }) => {
  const [showInput, setShowInput] = useState(false);
  const [filterLocation, setFilterLocation] = useState(null);
  const dropdownRef = useRef(null);

  const handleLocationSubmit = (displayName, latitude, longitude) => {
    const newLocation = { displayName, latitude, longitude };
    setFilterLocation(newLocation);
    onLocationChange(newLocation);
    setShowInput(false);
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setShowInput(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const showLocationAlert = () => {
    const message =
      "tribe works best with location services turned on. You'll be able to reach out and interact with tribes when you turn on location services for tribe.";

    if (window.confirm(message + "\n\nTurn on in settings?")) {
      if (navigator.userAgent.match(/iPhone|iPad|iPod/i)) {
        window.location.href = "app-settings:";
      } else if (navigator.userAgent.match(/Android/i)) {
        window.location.href =
          "intent://settings#Intent;scheme=android-app;end";
      } else {
        alert(
          "Unable to open settings on this device. Please enable location services manually."
        );
      }
    } else {
      // User chose to keep location services off
      console.log("User chose to keep location services off");
    }
  };

  if (!isLocationEnabled) {
    return (
      <div className="fixed top-28 left-1/2 transform -translate-x-1/2 z-40 w-auto">
        <button
          onClick={showLocationAlert}
          className="bg-zinc-950 text-white font-futura  px-4 py-2 rounded-full shadow-lg h-10 flex items-center justify-center transition-transform active:scale-95 lowercase"
        >
          location services are off &gt;
        </button>
      </div>
    );
  }

  return (
    <div className="fixed top-28 left-1/2 transform -translate-x-1/2 z-40 w-1/5 max-w-xs lowercase">
      <button
        onClick={() => setShowInput(!showInput)}
        className="w-full px-4 py-2 rounded-full shadow-lg h-10 flex items-center justify-center transition-transform active:scale-95 bg-zinc-950 text-white font-futura font-bold"
      >
        {filterLocation
          ? `filter: ${filterLocation.displayName.toLowerCase()}`
          : "filter"}
      </button>
      {showInput && (
        <div
          ref={dropdownRef}
          className="absolute top-full left-0 right-0 mt-2 bg-white p-4 rounded-lg shadow-lg"
        >
          <ManualLocationInput
            onSubmit={handleLocationSubmit}
            onCancel={() => setShowInput(false)}
          />
          {filterLocation && (
            <button
              onClick={() => {
                setFilterLocation(null);
                onLocationChange(null);
              }}
              className="mt-2 text-blue-500 underline transition-transform active:scale-95"
            >
              clear filter
            </button>
          )}
        </div>
      )}
    </div>
  );
};

export default LocationFilterButton;
