import { useState, useEffect, useCallback, useRef } from "react";
import { supabase } from "../utils/supabaseClient";
import { useAuth } from "../contexts/AuthContext";

export const useTribeToTribeChatLogic = (
  tribe,
  otherTribe,
  onSendMessage,
  setError
) => {
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);
  const [users, setUsers] = useState({});
  const [isSending, setIsSending] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const { user } = useAuth();
  const messagesEndRef = useRef(null);
  const chatIdRef = useRef(null);
  const fetchedRef = useRef(false);
  const subscriptionRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const fetchUserInfo = useCallback(
    async (userId) => {
      if (users[userId]) return users[userId];
      try {
        const { data, error } = await supabase
          .from("users")
          .select("username")
          .eq("id", userId)
          .single();
        if (error) throw error;
        setUsers((prevUsers) => ({ ...prevUsers, [userId]: data.username }));
        return data.username;
      } catch (error) {
        console.error("Error fetching user info:", error);
        return "Unknown user";
      }
    },
    [users]
  );

  const setupRealtimeSubscription = useCallback(() => {
    if (chatIdRef.current && !subscriptionRef.current) {
      const chatChannel = supabase.channel(
        `tribe_to_tribe_chat:${chatIdRef.current}`
      );

      chatChannel
        .on(
          "postgres_changes",
          {
            event: "INSERT",
            schema: "public",
            table: "tribe_to_tribe_messages",
            filter: `chat_id=eq.${chatIdRef.current}`,
          },
          async (payload) => {
            const username = await fetchUserInfo(payload.new.user_id);
            setMessages((currentMessages) => [
              ...currentMessages,
              { ...payload.new, username },
            ]);
            scrollToBottom();
          }
        )
        .subscribe((status) => {
          if (status === "SUBSCRIBED") {
            subscriptionRef.current = chatChannel;
          }
        });
    }
  }, [fetchUserInfo]);

  const fetchMessages = useCallback(async () => {
    if (!tribe?.id || !otherTribe?.id || fetchedRef.current) return;

    try {
      setIsLoading(true);
      const { data: chatData, error: chatError } = await supabase
        .from("tribe_to_tribe_chats")
        .select("id")
        .or(
          `and(tribe1_id.eq.${tribe.id},tribe2_id.eq.${otherTribe.id}),and(tribe1_id.eq.${otherTribe.id},tribe2_id.eq.${tribe.id})`
        )
        .single();

      if (chatError) throw chatError;

      chatIdRef.current = chatData.id;

      const { data, error: messagesError } = await supabase
        .from("tribe_to_tribe_messages")
        .select("*, user:users(username)")
        .eq("chat_id", chatData.id)
        .order("created_at", { ascending: true });

      if (messagesError) throw messagesError;

      const messagesWithUserInfo = await Promise.all(
        data.map(async (msg) => ({
          ...msg,
          username: await fetchUserInfo(msg.user_id),
        }))
      );

      setMessages(messagesWithUserInfo);
      fetchedRef.current = true;
      setupRealtimeSubscription();
    } catch (error) {
      console.error("Error in fetchMessages:", error);
      setError("Failed to load messages");
    } finally {
      setIsLoading(false);
    }
  }, [
    tribe?.id,
    otherTribe?.id,
    setError,
    fetchUserInfo,
    setupRealtimeSubscription,
  ]);

  useEffect(() => {
    fetchMessages();

    return () => {
      if (subscriptionRef.current) {
        supabase.removeChannel(subscriptionRef.current);
      }
      fetchedRef.current = false;
    };
  }, [fetchMessages]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const createSystemMessage = async (content) => {
    try {
      const { data, error } = await supabase
        .from("tribe_to_tribe_messages")
        .insert({
          chat_id: chatIdRef.current,
          content,
          user_id: user.id,
          tribe_id: tribe.id,
          is_system_message: true,
        })
        .single();

      if (error) throw error;

      setMessages((prevMessages) => [
        ...prevMessages,
        { ...data, username: "System" },
      ]);
    } catch (error) {
      console.error("Error creating system message:", error);
    }
  };

  const handleSend = async () => {
    if (!message.trim() || isSending) return;
    setIsSending(true);
    try {
      const newMessage = await onSendMessage(tribe, message, otherTribe);
      setMessage("");
      // Optimistically update the UI
      const username = await fetchUserInfo(newMessage.user_id);
      setMessages((prevMessages) => [
        ...prevMessages,
        { ...newMessage, username },
      ]);
      scrollToBottom();

      // Create system message for notification
      await createSystemMessage(
        `${tribe.name} has sent a message to ${otherTribe.name}`
      );
    } catch (error) {
      console.error("Error sending message:", error);
      setError("Failed to send message. Please try again.");
    } finally {
      setIsSending(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return {
    message,
    setMessage,
    messages,
    users,
    handleSend,
    handleKeyPress,
    user,
    isSending,
    isLoading,
    messagesEndRef,
  };
};
