import { useEffect, useRef } from "react";
import { supabase } from "../utils/supabaseClient";

export const useChatSubscription = (type, tribe, otherTribe, onNewMessage) => {
  const subscriptionRef = useRef(null);

  useEffect(() => {
    if (subscriptionRef.current) {
      supabase.removeChannel(subscriptionRef.current);
    }

    let channel;
    if (type === "tribe" && tribe?.id) {
      channel = supabase.channel(`internal_messages:${tribe.id}`);
      channel.on(
        "postgres_changes",
        {
          event: "INSERT",
          schema: "public",
          table: "internal_messages",
          filter: `tribe_id=eq.${tribe.id}`,
        },
        onNewMessage
      );
    } else if (type === "tribeToTribe" && otherTribe?.id) {
      supabase
        .from("tribe_to_tribe_chats")
        .select("id")
        .or(
          `and(tribe1_id.eq.${tribe.id},tribe2_id.eq.${otherTribe.id}),and(tribe1_id.eq.${otherTribe.id},tribe2_id.eq.${tribe.id})`
        )
        .single()
        .then(({ data }) => {
          if (data) {
            channel = supabase.channel(`tribe_to_tribe_messages:${data.id}`);
            channel.on(
              "postgres_changes",
              {
                event: "INSERT",
                schema: "public",
                table: "tribe_to_tribe_messages",
                filter: `chat_id=eq.${data.id}`,
              },
              onNewMessage
            );
          }
        });
    }

    if (channel) {
      channel.subscribe((status) => {
        if (status === "SUBSCRIBED") {
          subscriptionRef.current = channel;
        }
      });
    }

    return () => {
      if (subscriptionRef.current) {
        supabase.removeChannel(subscriptionRef.current);
      }
    };
  }, [type, tribe, otherTribe, onNewMessage]);
};
