import React, { useState } from "react";
import { useAuth } from "../../contexts/AuthContext";
import AuthForm from "../AuthForm";

const ProfilePanel = ({ user }) => {
  const { signOut, updateUserProfileImage } = useAuth();
  const [isUpdating, setIsUpdating] = useState(false);
  const [updateError, setUpdateError] = useState(null);

  const handleFileChange = async (event) => {
    const file = event.target.files[0];
    if (file) {
      setIsUpdating(true);
      setUpdateError(null);
      try {
        await updateUserProfileImage(file);
      } catch (error) {
        setUpdateError("Failed to update profile picture. Please try again.");
      } finally {
        setIsUpdating(false);
      }
    }
  };

  if (!user) {
    return <AuthForm />;
  }

  return (
    <div className="space-y-4">
      <div className="w-48 h-48 bg-gray-200 rounded-lg mx-auto flex items-center justify-center relative">
        <img
          src={user.profile_picture || "https://via.placeholder.com/150"}
          alt={user.username}
          className="w-full h-full object-cover rounded-lg"
        />
        <label
          htmlFor="profile-picture-upload"
          className="absolute bottom-2 right-2 bg-white rounded-full p-2 cursor-pointer"
        >
          <svg
            className="w-6 h-6 text-black"
            aria-hidden="true"
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            fill="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              fillRule="evenodd"
              d="M8 7V2.221a2 2 0 0 0-.5.365L3.586 6.5a2 2 0 0 0-.365.5H8Zm2 0V2h7a2 2 0 0 1 2 2v.126a5.087 5.087 0 0 0-4.74 1.368v.001l-6.642 6.642a3 3 0 0 0-.82 1.532l-.74 3.692a3 3 0 0 0 3.53 3.53l3.694-.738a3 3 0 0 0 1.532-.82L19 15.149V20a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V9h5a2 2 0 0 0 2-2Z"
              clipRule="evenodd"
            />
            <path
              fillRule="evenodd"
              d="M17.447 8.08a1.087 1.087 0 0 1 1.187.238l.002.001a1.088 1.088 0 0 1 0 1.539l-.377.377-1.54-1.542.373-.374.002-.001c.1-.102.22-.182.353-.237Zm-2.143 2.027-4.644 4.644-.385 1.924 1.925-.385 4.644-4.642-1.54-1.54Zm2.56-4.11a3.087 3.087 0 0 0-2.187.909l-6.645 6.645a1 1 0 0 0-.274.51l-.739 3.693a1 1 0 0 0 1.177 1.176l3.693-.738a1 1 0 0 0 .51-.274l6.65-6.646a3.088 3.088 0 0 0-2.185-5.275Z"
              clipRule="evenodd"
            />
          </svg>
        </label>
        <input
          id="profile-picture-upload"
          type="file"
          accept="image/*"
          onChange={handleFileChange}
          className="hidden"
        />
      </div>
      {isUpdating && (
        <p className="text-black-500 text-center">
          Updating profile picture...
        </p>
      )}
      {updateError && <p className="text-red-500 text-center">{updateError}</p>}
      <p className="text-center font-bold">{user.username}</p>
      <button
        onClick={signOut}
        className="w-full bg-zinc-950 text-white py-2 rounded-lg text-sm"
      >
        sign out
      </button>
    </div>
  );
};

export default ProfilePanel;
