import React from "react";

const getColorForUser = (userId, userColorMap) => {
  const colors = [
    "bg-[#4749fe]",
    "bg-[#ee285b]",
    "bg-[#ffd700]",
    "bg-green-100",
    "bg-purple-100",
  ];
  if (!userColorMap.has(userId)) {
    userColorMap.set(userId, colors[userColorMap.size % colors.length]);
  }
  return userColorMap.get(userId);
};

const MessageDisplay = ({ messages }) => {
  const userColorMap = new Map();

  return (
    <div className="w-full bg-white rounded-md mb-4 overflow-hidden">
      <div className="p-2 text-white text-sm">
        {messages.map((message) =>
          message.is_system_message ? (
            <div key={message.id} className="text-gray-600 italic mb-2">
              {message.content}
            </div>
          ) : (
            <div
              key={message.id}
              className={`rounded p-2 mb-2 ${getColorForUser(
                message.user_id,
                userColorMap
              )} text-left`}
            >
              <p className="font-bold">{message.users.username}:</p>
              <p>{message.content}</p>
            </div>
          )
        )}
      </div>
    </div>
  );
};

export default MessageDisplay;
