import React from "react";
import SidePanel from "./SidePanel";
import TribesPanel from "./panels/TribesPanel";
import ChatsPanel from "./panels/ChatsPanel";
import ProfilePanel from "./panels/ProfilePanel";

const PanelManager = ({
  user,
  tribes,
  tribeToTribeChats,
  showTribesPanel,
  showChatsPanel,
  showProfilePanel,
  onClosePanels,
  onCreateTribe,
  onSelectChat,
  onApproveTribeInvitation,
  onDeclineTribeInvitation,
}) => {
  return (
    <>
      <SidePanel
        isOpen={showTribesPanel}
        onClose={onClosePanels}
        title="Your Tribes"
        content={
          <TribesPanel
            user={user}
            tribes={tribes}
            onCreateTribe={onCreateTribe}
            onSelectChat={onSelectChat}
            onApproveTribeInvitation={onApproveTribeInvitation}
            onDeclineTribeInvitation={onDeclineTribeInvitation}
          />
        }
      />
      <SidePanel
        isOpen={showChatsPanel}
        onClose={onClosePanels}
        title="Your Chats"
        content={
          <ChatsPanel
            user={user}
            tribeToTribeChats={tribeToTribeChats}
            onSelectChat={onSelectChat}
          />
        }
      />
      <SidePanel
        isOpen={showProfilePanel}
        onClose={onClosePanels}
        title={user ? "your profile." : "login."}
        content={<ProfilePanel user={user} />}
      />
    </>
  );
};

export default PanelManager;
