import React from "react";

const SwitchFormButton = ({ isSignUp, setIsSignUp }) => {
  return (
    <button
      type="button"
      onClick={() => setIsSignUp(!isSignUp)}
      className="w-full px-4 py-2 bg-white text-black border font-bold border-black-500 rounded-md hover:bg-zinc-950 hover:text-white"
    >
      {isSignUp ? "switch to sign in" : "switch to sign up"}
    </button>
  );
};

export default SwitchFormButton;
