import { supabase } from "../utils/supabaseClient";
import { uploadImage } from "../utils/imageUpload";
import { DEFAULT_PROFILE_PICTURE } from "../utils/constants";

export const useSignUp = () => {
  const signUp = async ({ email, password, username, profile_picture }) => {
    try {
      const cleanUsername = username.startsWith("@")
        ? username.slice(1)
        : username;
      const { data, error } = await supabase.auth.signUp({ email, password });

      if (error) throw error;

      if (data.user) {
        let profile_picture_url = profile_picture || DEFAULT_PROFILE_PICTURE;

        if (profile_picture instanceof File) {
          try {
            profile_picture_url = await uploadImage(profile_picture);
          } catch (uploadError) {
            console.error("Error uploading profile picture:", uploadError);
            profile_picture_url = DEFAULT_PROFILE_PICTURE;
          }
        }

        const { data: insertData, error: insertError } = await supabase
          .from("users")
          .insert({
            id: data.user.id,
            email: data.user.email,
            username: cleanUsername,
            profile_picture: profile_picture_url,
          })
          .select();

        if (insertError) {
          console.error("Error inserting user data:", insertError);
          return { user: null, error: insertError.message };
        }

        return {
          user: { ...data.user, ...insertData[0] },
          message:
            "Sign up successful! Please check your email for verification.",
        };
      } else {
        return { user: null, error: "No user data returned from sign up" };
      }
    } catch (error) {
      console.error("Error during sign up:", error);
      return {
        user: null,
        error: error.message || "An unknown error occurred during sign up",
      };
    }
  };

  return { signUp };
};
