import React, { useState, useEffect } from "react";

const TribesPanel = ({
  user,
  tribes,
  loading,
  error,
  onCreateTribe,
  onSelectChat,
  onApproveTribeInvitation,
  onDeclineTribeInvitation,
}) => {
  const [newTribeName, setNewTribeName] = useState("");
  const [createError, setCreateError] = useState(null);
  const [message, setMessage] = useState(null);
  const [isCreatingTribe, setIsCreatingTribe] = useState(false);
  const [sortedTribes, setSortedTribes] = useState([]);

  useEffect(() => {
    if (tribes && tribes.length > 0) {
      const sorted = [...tribes].sort(
        (a, b) => new Date(b.last_activity) - new Date(a.last_activity)
      );
      setSortedTribes(sorted);
    }
  }, [tribes]);

  const handleCreateTribe = async () => {
    setCreateError(null);
    setMessage(null);
    setIsCreatingTribe(true);
    try {
      await onCreateTribe(newTribeName);
      setNewTribeName("");
      setMessage("tribe created successfully!");
    } catch (err) {
      setCreateError(
        err.message || "failed to create tribe. please try again."
      );
    } finally {
      setIsCreatingTribe(false);
    }
  };

  if (!user) {
    return (
      <p className="text-sm text-gray-500 lowercase">
        please login to view tribes.
      </p>
    );
  }

  return (
    <div className="flex flex-col h-full lowercase">
      <div className="flex-shrink-0 p-4 border-b">
        <input
          type="text"
          value={newTribeName}
          onChange={(e) => setNewTribeName(e.target.value.toLowerCase())}
          placeholder="new tribe name"
          className="w-full border rounded px-2 py-1 text-sm mb-2"
          disabled={isCreatingTribe}
        />
        <button
          onClick={handleCreateTribe}
          className={`w-full bg-zinc-950 text-white px-2 py-1 rounded text-sm ${
            isCreatingTribe ? "opacity-50 cursor-not-allowed" : ""
          }`}
          disabled={isCreatingTribe}
        >
          {isCreatingTribe ? "creating..." : "create tribe"}
        </button>
        {createError && (
          <p className="text-red-500 text-xs mt-1">{createError}</p>
        )}
        {message && <p className="text-green-500 text-xs mt-1">{message}</p>}
      </div>
      <div className="flex-grow overflow-y-auto custom-scrollbar">
        {loading ? (
          <p className="text-sm text-gray-500 p-4">loading tribes...</p>
        ) : error ? (
          <p className="text-sm text-red-500 p-4">error: {error}</p>
        ) : sortedTribes.length > 0 ? (
          sortedTribes.map((tribe) => (
            <div
              key={tribe.id}
              className="flex items-center justify-between p-2 hover:bg-gray-100"
            >
              <button
                onClick={() => {
                  if (tribe.status === "active") {
                    onSelectChat({ type: "tribe", tribe });
                  }
                }}
                className={`w-full text-left p-2 rounded text-sm truncate ${
                  tribe.status !== "active"
                    ? "cursor-not-allowed opacity-50"
                    : ""
                } ${tribe.hasUnreadMessages ? "font-bold" : ""}`}
                disabled={tribe.status !== "active"}
              >
                {tribe.name.toLowerCase()}
              </button>
              {tribe.status === "invited" && (
                <div className="flex space-x-2">
                  <button
                    onClick={() => onApproveTribeInvitation(tribe.id)}
                    className="bg-green-500 text-white px-2 py-1 rounded text-xs"
                  >
                    approve
                  </button>
                  <button
                    onClick={() => onDeclineTribeInvitation(tribe.id)}
                    className="bg-red-500 text-white px-2 py-1 rounded text-xs"
                  >
                    decline
                  </button>
                </div>
              )}
            </div>
          ))
        ) : (
          <p className="text-sm text-gray-500 p-4">
            no tribes yet. create one to get started!
          </p>
        )}
      </div>
    </div>
  );
};

export default TribesPanel;
