import React, { useCallback, useMemo, useState, useEffect } from "react";
import ChatHeader from "./chat/ChatHeader";
import MessageList from "./chat/MessageList";
import MessageInput from "./chat/MessageInput";
import MembersList from "./chat/MembersList";
import AddMemberForm from "./AddMemberForm";
import { useChatLogic } from "../hooks/useChatLogic";
import { supabase } from "../utils/supabaseClient";

const Chat = React.memo(
  ({
    type,
    tribe,
    otherTribe,
    onClose,
    onSendMessage,
    onLeaveTribe,
    onAddMember,
  }) => {
    const {
      message,
      setMessage,
      messages,
      error,
      users,
      handleSend,
      handleKeyPress,
      user,
      isSending,
      isLoading,
      messagesEndRef,
    } = useChatLogic(
      type,
      tribe,
      otherTribe,
      onSendMessage,
      onLeaveTribe,
      onAddMember
    );

    const [showAddMember, setShowAddMember] = useState(false);
    const [showMembers, setShowMembers] = useState(false);
    const [members, setMembers] = useState([]);
    const [showLeaveConfirmation, setShowLeaveConfirmation] = useState(false);

    const fetchMembers = useCallback(async () => {
      try {
        const { data, error } = await supabase
          .from("tribe_members")
          .select("user_id, users(username)")
          .eq("tribe_id", tribe.id);

        if (error) throw error;

        setMembers(
          data.map((member) => ({
            id: member.user_id,
            username: member.users.username,
          }))
        );
        setShowMembers(true);
      } catch (error) {
        console.error("error fetching members:", error);
      }
    }, [tribe.id]);

    const handleAddMemberSubmit = useCallback(
      async (userId, tribeName) => {
        try {
          await onAddMember(tribe.id, userId);
          setShowAddMember(false);
          fetchMembers();
        } catch (error) {
          console.error("error adding member:", error);
        }
      },
      [onAddMember, tribe.id, fetchMembers]
    );

    const handleLeaveTribeClick = useCallback(() => {
      setShowLeaveConfirmation(true);
    }, []);

    const confirmLeaveTribe = useCallback(async () => {
      try {
        await onLeaveTribe(tribe.id);
        onClose();
      } catch (error) {
        console.error("error leaving tribe:", error);
      }
    }, [onLeaveTribe, tribe.id, onClose]);

    const chatHeader = useMemo(
      () => (
        <ChatHeader
          type={type}
          tribe={tribe}
          otherTribe={otherTribe}
          onClose={onClose}
          onLeaveTribe={handleLeaveTribeClick}
          onShowMembers={fetchMembers}
          onAddMember={() => setShowAddMember(true)}
        />
      ),
      [type, tribe, otherTribe, onClose, handleLeaveTribeClick, fetchMembers]
    );

    if (isLoading) {
      return (
        <div className="fixed inset-0 bg-white z-50 flex items-center justify-center lowercase">
          <p>loading chat...</p>
        </div>
      );
    }

    return (
      <div className="fixed inset-0 top-4 bg-white z-50 flex flex-col pt-16 lowercase">
        {chatHeader}
        <MessageList
          messages={messages}
          user={user}
          messagesEndRef={messagesEndRef}
          type={type}
        />
        <MessageInput
          message={message}
          setMessage={setMessage}
          handleSend={handleSend}
          handleKeyPress={handleKeyPress}
          isSending={isSending}
        />
        {error && (
          <div
            className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative"
            role="alert"
          >
            <span className="block sm:inline">{error}</span>
          </div>
        )}
        {showAddMember && (
          <AddMemberForm
            onAddMember={handleAddMemberSubmit}
            onClose={() => setShowAddMember(false)}
            tribeName={tribe.name}
            currentMembers={members.map((member) => member.id)}
          />
        )}
        {showMembers && (
          <MembersList
            members={members}
            onClose={() => setShowMembers(false)}
          />
        )}
        {showLeaveConfirmation && (
          <div className="fixed inset-0 bg-zinc-950 bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white p-6 rounded-lg">
              <p className="mb-4">are you sure you want to leave this tribe?</p>
              <div className="flex justify-end space-x-2">
                <button
                  onClick={() => setShowLeaveConfirmation(false)}
                  className="px-4 py-2 bg-gray-200 rounded-md"
                >
                  cancel
                </button>
                <button
                  onClick={confirmLeaveTribe}
                  className="px-4 py-2 bg-red-500 text-white rounded-md"
                >
                  leave tribe
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
);

export default Chat;
