import React, { Suspense } from "react";
import { useAuth } from "../contexts/AuthContext";
import { useAppData } from "../hooks/useAppData";
import ErrorBoundary from "../components/ErrorBoundary";
import AppUI from "./AppUI";
import Header from "../components/Header";
import useLocationUpdater from "../hooks/useLocationUpdater";
import { useAppState } from "../hooks/useAppState";
import { useDistanceCalculation } from "../hooks/useDistanceCalculation";
import { useRealtimeSubscription } from "../hooks/useRealtimeSubscription";
import QuickActionsButton from "../components/QuickActionsButton";
import { useLocation } from "../hooks/useLocation";

const App = () => {
  const { user, loading: authLoading } = useAuth();
  const {
    tribes,
    moments,
    tribeToTribeChats,
    loading: appDataLoading,
    error: appDataError,
    handleCreateTribe,
    handleSendMessage,
    handleAddMember,
    handleLeaveTribe,
    fetchData,
    handleApproveTribeInvitation,
    handleDeclineTribeInvitation,
  } = useAppData();
  const { error: locationUpdaterError } = useLocationUpdater();

  const {
    showModal,
    setShowModal,
    showTribesPanel,
    setShowTribesPanel,
    showChatsPanel,
    setShowChatsPanel,
    showProfilePanel,
    setShowProfilePanel,
    selectedChat,
    setSelectedChat,
    selectedMoment,
    setSelectedMoment,
    appError,
    setAppError,
  } = useAppState();

  const {
    userLocation,
    error: locationError,
    status: locationStatus,
    requestLocation,
  } = useLocation();

  const { filterTribesByDistance } = useDistanceCalculation();

  useRealtimeSubscription(user, fetchData);

  React.useEffect(() => {
    if (locationUpdaterError) {
      setAppError(locationUpdaterError);
    }
  }, [locationUpdaterError, setAppError]);

  React.useEffect(() => {
    if (locationError) {
      setAppError(locationError);
    }
  }, [locationError, setAppError]);

  React.useEffect(() => {
    if (appDataError) {
      setAppError(appDataError);
    }
  }, [appDataError, setAppError]);

  if (authLoading || appDataLoading) {
    return (
      <div className="flex justify-center items-center h-screen lowercase">
        loading...
      </div>
    );
  }

  const isLocationEnabled = locationStatus === "granted";

  return (
    <ErrorBoundary>
      <Suspense fallback={<div className="lowercase">loading...</div>}>
        <div className="min-h-screen bg-white text-black relative lowercase">
          <Header
            user={user}
            onProfileClick={() => setShowProfilePanel(true)}
          />
          <div className="relative z-0 pt-16">
            <AppUI
              user={user}
              tribes={tribes}
              moments={moments}
              tribeToTribeChats={tribeToTribeChats}
              showModal={showModal}
              showTribesPanel={showTribesPanel}
              showChatsPanel={showChatsPanel}
              showProfilePanel={showProfilePanel}
              selectedChat={selectedChat}
              selectedMoment={selectedMoment}
              appError={appError}
              onMomentClick={setSelectedMoment}
              onClosePanels={() => {
                setShowTribesPanel(false);
                setShowChatsPanel(false);
                setShowProfilePanel(false);
              }}
              onCreateTribe={handleCreateTribe}
              onSendMessage={handleSendMessage}
              onAddMember={handleAddMember}
              onLeaveTribe={handleLeaveTribe}
              onSignInClick={() => setShowProfilePanel(true)}
              onOpenChat={(tribe1, tribe2) => {
                setSelectedChat({
                  type: "tribeToTribe",
                  tribe: tribe1,
                  otherTribe: tribe2,
                });
                setShowModal(false);
              }}
              getUserTribe={(momentTribeId) =>
                tribes.find((tribe) => tribe.id === momentTribeId)
              }
              onApproveTribeInvitation={handleApproveTribeInvitation}
              onDeclineTribeInvitation={handleDeclineTribeInvitation}
              setShowModal={setShowModal}
              setShowTribesPanel={setShowTribesPanel}
              setShowChatsPanel={setShowChatsPanel}
              setShowProfilePanel={setShowProfilePanel}
              setSelectedChat={setSelectedChat}
              setAppError={setAppError}
              loading={appDataLoading}
              error={appError}
              fetchData={fetchData}
              userTribes={tribes}
              userLocation={userLocation}
              requestLocation={requestLocation}
              locationStatus={locationStatus}
              isLocationEnabled={isLocationEnabled}
              momentsLoading={appDataLoading}
              momentsError={appDataError}
            />
          </div>
          <QuickActionsButton />
        </div>
      </Suspense>
    </ErrorBoundary>
  );
};

export default App;
