import { supabase } from "../utils/supabaseClient";
import { DEFAULT_PROFILE_PICTURE } from "../utils/constants";

export const useSignIn = (setUser) => {
  const signIn = async ({ username, password }) => {
    try {
      const cleanUsername = username.startsWith("@")
        ? username.slice(1)
        : username;

      console.log("Attempting to sign in with username:", cleanUsername);

      const { data: userData, error: userError } = await supabase
        .from("users")
        .select("email")
        .eq("username", cleanUsername)
        .single();

      if (userError) {
        console.error("Error fetching user data:", userError);
        if (userError.code === "PGRST116") {
          throw new Error("User not found");
        }
        throw userError;
      }

      if (!userData) {
        throw new Error("User not found");
      }

      console.log("User found:", userData);

      const { data, error } = await supabase.auth.signInWithPassword({
        email: userData.email,
        password,
      });

      if (error) {
        console.error("Error signing in:", error);
        throw error;
      }

      console.log("Auth sign-in successful:", data.user);

      const { data: existingUser, error: checkError } = await supabase
        .from("users")
        .select("*")
        .eq("id", data.user.id)
        .single();

      if (checkError) {
        console.error("Error checking existing user:", checkError);
        throw checkError;
      }

      let fullUserData;

      if (!existingUser) {
        console.log("User not found in public.users table. Creating entry...");
        const { data: newUserData, error: insertError } = await supabase
          .from("users")
          .insert({
            id: data.user.id,
            email: data.user.email,
            username: cleanUsername,
            profile_picture: DEFAULT_PROFILE_PICTURE,
          })
          .select()
          .single();

        if (insertError) {
          console.error("Error creating user entry:", insertError);
          throw new Error("Failed to create user profile. Please try again.");
        }

        console.log("New user entry created:", newUserData);
        fullUserData = newUserData;
      } else {
        console.log("Existing user found:", existingUser);
        fullUserData = existingUser;
      }

      const updatedUser = { ...data.user, ...fullUserData };
      console.log("Final user data:", updatedUser);
      setUser(updatedUser);
      return { user: updatedUser, error: null };
    } catch (error) {
      console.error("Error during sign in:", error);
      return { user: null, error };
    }
  };

  return { signIn };
};
