import { supabase } from "../utils/supabaseClient";

export const useTribeMessages = (user) => {
  const handleCreateTribeMessage = async (
    tribeId,
    content,
    isSystemMessage = false
  ) => {
    try {
      await supabase.from("internal_messages").insert({
        content: content,
        tribe_id: tribeId,
        user_id: user.id,
        is_system_message: isSystemMessage,
      });
    } catch (error) {
      console.error("Error in handleCreateTribeMessage:", error);
      throw error;
    }
  };

  return { handleCreateTribeMessage };
};
