import { useState, useCallback, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

export const useTribeToTribeChats = (user, tribes) => {
  const [tribeToTribeChats, setTribeToTribeChats] = useState([]);
  const [error, setError] = useState(null);

  const fetchTribeToTribeChats = useCallback(async () => {
    if (!user || !tribes.length) return;

    try {
      const { data: chatsData, error: chatsError } = await supabase
        .from("tribe_to_tribe_chats")
        .select(
          `
          id, last_activity,
          tribe1:tribes!tribe_to_tribe_chats_tribe1_id_fkey(id, name, created_by),
          tribe2:tribes!tribe_to_tribe_chats_tribe2_id_fkey(id, name, created_by),
          chat_participants(has_unread_messages, user_id)
        `
        )
        .or(
          `tribe1_id.in.(${tribes
            .map((t) => t.id)
            .join(",")}),tribe2_id.in.(${tribes.map((t) => t.id).join(",")})`
        )
        .order("last_activity", { ascending: false });

      if (chatsError) throw chatsError;

      console.log("Fetched tribe-to-tribe chats:", chatsData);

      setTribeToTribeChats(
        chatsData.map((chat) => ({
          ...chat,
          hasUnreadMessages: chat.chat_participants.some(
            (participant) =>
              participant.user_id === user.id && participant.has_unread_messages
          ),
        }))
      );
    } catch (error) {
      console.error("Error fetching tribe-to-tribe chats:", error);
      setError("Failed to fetch tribe-to-tribe chats");
    }
  }, [user, tribes]);

  useEffect(() => {
    fetchTribeToTribeChats();
  }, [fetchTribeToTribeChats]);

  return { tribeToTribeChats, fetchTribeToTribeChats, error };
};
