import { useState, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

export const useTribeModal = (
  tribes,
  moment,
  onSendMessage,
  onOpenChat,
  onClose
) => {
  const [selectedTribe, setSelectedTribe] = useState("");
  const [message, setMessage] = useState("");
  const [showMessageForm, setShowMessageForm] = useState(false);
  const [error, setError] = useState(null);
  const [contactedTribes, setContactedTribes] = useState({});
  const [isSending, setIsSending] = useState(false);

  useEffect(() => {
    if (tribes.length > 0 && moment && moment.tribe) {
      setSelectedTribe("");
      fetchContactedTribes();
    }
  }, [tribes, moment]);

  const fetchContactedTribes = async () => {
    if (!moment || !moment.tribe) return;

    const { data, error } = await supabase
      .from("tribe_to_tribe_chats")
      .select("tribe1_id, tribe2_id")
      .or(`tribe1_id.eq.${moment.tribe.id},tribe2_id.eq.${moment.tribe.id}`);

    if (error) {
      console.error("Error fetching contacted tribes:", error);
      return;
    }

    const contacted = data.reduce((acc, chat) => {
      const otherTribeId =
        chat.tribe1_id === moment.tribe.id ? chat.tribe2_id : chat.tribe1_id;
      acc[otherTribeId] = true;
      return acc;
    }, {});

    setContactedTribes(contacted);
  };

  const handleSendMessage = async () => {
    if (selectedTribe && message && moment && moment.tribe) {
      const tribe = tribes.find((t) => t.id === selectedTribe);
      if (tribe) {
        setIsSending(true);
        setError(null);
        try {
          const result = await onSendMessage(tribe, message, moment.tribe);
          setSelectedTribe("");
          setMessage("");
          onClose();
          if (result) {
            onOpenChat({
              type: "tribeToTribe",
              tribe,
              otherTribe: moment.tribe,
            });
          }
        } catch (error) {
          console.error("Error sending message:", error);
          setError("Failed to send message. Please try again.");
        } finally {
          setIsSending(false);
        }
      } else {
        setError("Selected tribe not found");
      }
    } else {
      setError("Please select a tribe and enter a message");
    }
  };

  const isSendDisabled = !selectedTribe || !message || isSending;

  return {
    selectedTribe,
    setSelectedTribe,
    message,
    setMessage,
    showMessageForm,
    setShowMessageForm,
    error,
    contactedTribes,
    handleSendMessage,
    isSendDisabled,
    isSending,
  };
};
