import React, { useState, useEffect, useCallback } from "react";
import { useTribeToTribeChatLogic } from "../hooks/useTribeToTribeChatLogic";
import { supabase } from "../utils/supabaseClient";

const TribeToTribeChat = ({
  tribe,
  otherTribe,
  onClose,
  onSendMessage,
  chatName,
}) => {
  const [error, setError] = useState(null);
  const [showMembers, setShowMembers] = useState(false);
  const [members, setMembers] = useState([]);
  const {
    message,
    setMessage,
    messages,
    users,
    handleSend,
    handleKeyPress,
    user,
    isSending,
    isLoading,
    messagesEndRef,
  } = useTribeToTribeChatLogic(tribe, otherTribe, onSendMessage, setError);

  const fetchMembers = useCallback(async () => {
    try {
      const { data: tribe1Members, error: error1 } = await supabase
        .from("tribe_members")
        .select("user_id, users(username)")
        .eq("tribe_id", tribe.id);

      const { data: tribe2Members, error: error2 } = await supabase
        .from("tribe_members")
        .select("user_id, users(username)")
        .eq("tribe_id", otherTribe.id);

      if (error1) throw error1;
      if (error2) throw error2;

      const allMembers = [
        ...tribe1Members.map((m) => ({ ...m, tribeName: tribe.name })),
        ...tribe2Members.map((m) => ({ ...m, tribeName: otherTribe.name })),
      ];

      setMembers(allMembers);
      setShowMembers(true);
    } catch (error) {
      console.error("Error fetching members:", error);
      setError("Failed to fetch members");
    }
  }, [tribe.id, otherTribe.id]);

  if (isLoading) {
    return (
      <div className="fixed inset-0 bg-zinc-950 text-white z-50 flex items-center justify-center">
        <p>loading chat...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="fixed inset-0 top-4 bg-gray-900 text-white z-50 flex flex-col items-center justify-center">
        <p className="text-red-500 mb-4">{error}</p>
        <button onClick={onClose} className="text-black">
          <svg
            className="w-6 h-6"
            aria-hidden="true"
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            fill="none"
            viewBox="0 0 24 24"
          >
            <path
              stroke="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d="M6 18 17.94 6M18 18 6.06 6"
            />
          </svg>
        </button>
      </div>
    );
  }

  return (
    <div className="fixed inset-0 bg-zinc-950 text-white z-50 flex flex-col pt-20">
      <div className="bg-white text-black border border-white-100 p-4 flex justify-between items-center">
        <h2 className="text-xl font-bold lowercase">
          {chatName ||
            `${tribe?.name || "unknown"} → ${otherTribe?.name || "unknown"}`}
        </h2>
        <div className="flex items-center space-x-2">
          <button
            onClick={fetchMembers}
            className="text-black p-2 transition-transform active:scale-95 lowercase"
            title="Show Members"
          >
            <svg
              className="w-6 h-6"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                fillRule="evenodd"
                d="M12 6a3.5 3.5 0 1 0 0 7 3.5 3.5 0 0 0 0-7Zm-1.5 8a4 4 0 0 0-4 4 2 2 0 0 0 2 2h7a2 2 0 0 0 2-2 4 4 0 0 0-4-4h-3Zm6.82-3.096a5.51 5.51 0 0 0-2.797-6.293 3.5 3.5 0 1 1 2.796 6.292ZM19.5 18h.5a2 2 0 0 0 2-2 4 4 0 0 0-4-4h-1.1a5.503 5.503 0 0 1-.471.762A5.998 5.998 0 0 1 19.5 18ZM4 7.5a3.5 3.5 0 0 1 5.477-2.889 5.5 5.5 0 0 0-2.796 6.293A3.501 3.501 0 0 1 4 7.5ZM7.1 12H6a4 4 0 0 0-4 4 2 2 0 0 0 2 2h.5a5.998 5.998 0 0 1 3.071-5.238A5.505 5.505 0 0 1 7.1 12Z"
                clipRule="evenodd"
              />
            </svg>
          </button>
          <button onClick={onClose} className="text-black">
            <svg
              className="w-6 h-6"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M6 18 17.94 6M18 18 6.06 6"
              />
            </svg>
          </button>
        </div>
      </div>
      <div className="flex-1 overflow-y-auto p-4">
        {messages.map((msg) => (
          <div
            key={msg.id}
            className={`mb-4 ${
              msg.user_id === user?.id ? "text-right" : "text-left"
            }`}
          >
            <span
              className={`inline-block rounded-lg py-2 px-3 ${
                msg.user_id === user?.id ? "bg-[#4749fe]" : "bg-[#ee285b]"
              }`}
            >
              <p>{msg.content}</p>
              <p className="text-xs text-white mt-1">
                {msg.username || users[msg.user_id] || "unknown user"}
              </p>
            </span>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>
      <div className="p-4 border-t border-gray-700 flex">
        <input
          type="text"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          onKeyPress={handleKeyPress}
          className="flex-1 border rounded-l-lg px-3 py-2 bg-gray-700 text-white lowercase"
          placeholder="type a message..."
        />
        <button
          onClick={handleSend}
          disabled={isSending}
          className={`bg-white text-black px-4 py-2 rounded-r-lg ${
            isSending ? "opacity-50 cursor-not-allowed" : ""
          }`}
        >
          {isSending ? "sending..." : "send"}
        </button>
      </div>
      {showMembers && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center">
          <div className="bg-white p-6 rounded-lg w-80">
            <h3 className="text-lg font-bold mb-4 text-black">Tribe Members</h3>
            <ul className="list-disc pl-5 text-black">
              {members.map((member) => (
                <li key={member.user_id}>
                  {member.users.username} ({member.tribeName})
                </li>
              ))}
            </ul>
            <button
              onClick={() => setShowMembers(false)}
              className="mt-4 bg-black text-white px-4 py-2 rounded"
            >
              Close
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default TribeToTribeChat;
