import React, { useRef, useEffect } from "react";

const MessageList = ({ messages, user, type }) => {
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  return (
    <div className="flex-1 overflow-y-auto p-4">
      {messages.map((msg) => (
        <div
          key={msg.id}
          className={`mb-4 ${
            msg.is_system_message
              ? "text-center"
              : msg.user_id === user?.id
              ? "text-right"
              : "text-left"
          }`}
        >
          {msg.is_system_message ? (
            <span
              className={`inline-block text-sm ${
                type === "tribeToTribe" ? "text-gray-400" : "text-gray-600"
              } italic`}
            >
              {msg.content}
            </span>
          ) : (
            <span
              className={`inline-block text-white rounded-lg py-2 px-3 ${
                msg.user_id === user?.id ? "bg-[#4749fe]" : "bg-[#ee285b]"
              }`}
            >
              <p>{msg.content}</p>
              <p className="text-xs text-white mt-1">
                {msg.username || "Unknown user"}
              </p>
            </span>
          )}
        </div>
      ))}
      <div ref={messagesEndRef} />
    </div>
  );
};

export default MessageList;
