import { supabase } from "../utils/supabaseClient";

export const fetchTribes = async (userId) => {
  console.log("Fetching tribes for user:", userId);
  const { data: tribesData, error: tribesError } = await supabase
    .from("tribes")
    .select(
      `
      *,
      tribe_members!inner(status, has_unread_messages, user_id)
    `
    )
    .eq("tribe_members.user_id", userId);

  if (tribesError) {
    console.error("Error fetching tribes:", tribesError);
    throw tribesError;
  }

  console.log("Fetched tribes:", tribesData);
  return tribesData;
};

export const createTribe = async (userId, tribeName, latitude, longitude) => {
  console.log("Creating tribe:", { userId, tribeName, latitude, longitude });
  const tribeData = {
    name: tribeName,
    created_by: userId,
  };

  if (latitude !== null && longitude !== null) {
    tribeData.latitude = latitude;
    tribeData.longitude = longitude;
  }

  const { data, error } = await supabase
    .from("tribes")
    .insert(tribeData)
    .select()
    .single();

  if (error) {
    console.error("Error creating tribe:", error);
    throw error;
  }

  console.log("Created tribe:", data);
  return data;
};
