import { useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

export const useRealtimeSubscription = (user, fetchData) => {
  useEffect(() => {
    if (user) {
      const channel = supabase
        .channel("realtime app")
        .on(
          "postgres_changes",
          {
            event: "INSERT",
            schema: "public",
            table: "tribe_members",
            filter: `user_id=eq.${user.id} AND status=eq.invited`,
          },
          () => {
            fetchData();
          }
        )
        .on(
          "postgres_changes",
          { event: "INSERT", schema: "public", table: "tribe_to_tribe_chats" },
          () => {
            fetchData();
          }
        )
        .on(
          "postgres_changes",
          {
            event: "UPDATE",
            schema: "public",
            table: "chat_participants",
            filter: `user_id=eq.${user.id} AND has_unread_messages=eq.true`,
          },
          () => {
            fetchData();
          }
        )
        .subscribe();

      return () => {
        supabase.removeChannel(channel);
      };
    }
  }, [user, fetchData]);
};
