import React, { useState, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

const AddMemberForm = ({ onAddMember, onClose, tribeName, currentMembers }) => {
  const [username, setUsername] = useState("@");
  const [searchResults, setSearchResults] = useState([]);
  const [error, setError] = useState("");
  const [isAdding, setIsAdding] = useState(false);
  const [selectedUser, setSelectedUser] = useState(null);

  useEffect(() => {
    if (username.length > 1) {
      searchUsers(username);
    } else {
      setSearchResults([]);
    }
  }, [username]);

  const searchUsers = async (searchTerm) => {
    try {
      const { data, error } = await supabase
        .from("users")
        .select("id, username")
        .ilike("username", `${searchTerm.replace("@", "")}%`)
        .limit(5);

      if (error) throw error;
      setSearchResults(
        data.filter(
          (user) =>
            !currentMembers.includes(user.id) &&
            user.username.toLowerCase() !== searchTerm.toLowerCase()
        )
      );
    } catch (error) {
      console.error("error searching users:", error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (username.length <= 1) {
      setError("username cannot be empty");
      return;
    }
    setIsAdding(true);
    try {
      let userToAdd = selectedUser;
      if (!userToAdd) {
        const exactMatch = searchResults.find(
          (user) =>
            user.username.toLowerCase() ===
            username.toLowerCase().replace("@", "")
        );
        if (exactMatch) {
          userToAdd = exactMatch;
        } else {
          throw new Error("user not found");
        }
      }
      await onAddMember(userToAdd.id, tribeName);
      onClose();
    } catch (error) {
      console.error("error adding member:", error);
      setError(error.message || "failed to add member. please try again.");
    } finally {
      setIsAdding(false);
    }
  };

  const handleUsernameChange = (e) => {
    let value = e.target.value;
    if (!value.startsWith("@")) {
      value = "@" + value;
    }
    setUsername(value);
    setSelectedUser(null);
  };

  const handleSelectUser = (user) => {
    setUsername("@" + user.username);
    setSelectedUser(user);
    setSearchResults([]);
  };

  return (
    <div className="fixed inset-0 bg-zinc-950 bg-opacity-50 flex justify-center items-center lowercase">
      <div className="bg-white p-6 rounded-lg w-full max-w-md">
        <h2 className="text-xl font-bold mb-4">add member to tribe</h2>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="relative">
            <input
              type="text"
              value={username}
              onChange={handleUsernameChange}
              placeholder="username"
              className="w-full px-3 py-2 border rounded-md"
            />
            {searchResults.length > 0 && (
              <div className="absolute z-10 w-full bg-white border border-gray-300 mt-1 rounded-md shadow-lg">
                {searchResults.map((result) => (
                  <div
                    key={result.id}
                    className="px-3 py-2 hover:bg-gray-100 cursor-pointer"
                    onClick={() => handleSelectUser(result)}
                  >
                    @{result.username}
                  </div>
                ))}
              </div>
            )}
          </div>
          {error && <p className="text-red-500">{error}</p>}
          <div className="flex justify-end space-x-2">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 text-gray-600 bg-gray-200 rounded-md hover:bg-gray-300"
              disabled={isAdding}
            >
              cancel
            </button>
            <button
              type="submit"
              className={`px-4 py-2 text-white bg-[#4749fe] rounded-md hover:bg-blue-600 ${
                isAdding ? "opacity-50 cursor-not-allowed" : ""
              }`}
              disabled={isAdding}
            >
              {isAdding ? "adding..." : "add"}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AddMemberForm;
