import { useState, useCallback } from "react";
import { supabase } from "../utils/supabaseClient";

export const useChatMessages = (type, tribe, otherTribe) => {
  const [messages, setMessages] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchMessages = useCallback(
    async (fetchUserInfo) => {
      if (!tribe?.id || (type === "tribeToTribe" && !otherTribe?.id)) return;

      setIsLoading(true);
      setError(null);

      try {
        let data;
        if (type === "tribe") {
          const { data: internalMessages, error } = await supabase
            .from("internal_messages")
            .select("*")
            .eq("tribe_id", tribe.id)
            .order("created_at", { ascending: true });
          if (error) throw error;
          data = internalMessages;
        } else if (type === "tribeToTribe") {
          const { data: chatData, error: chatError } = await supabase
            .from("tribe_to_tribe_chats")
            .select("id")
            .or(
              `and(tribe1_id.eq.${tribe.id},tribe2_id.eq.${otherTribe.id}),and(tribe1_id.eq.${otherTribe.id},tribe2_id.eq.${tribe.id})`
            )
            .single();
          if (chatError) throw chatError;

          const { data: tribeToTribeMessages, error } = await supabase
            .from("tribe_to_tribe_messages")
            .select("*")
            .eq("chat_id", chatData.id)
            .order("created_at", { ascending: true });
          if (error) throw error;
          data = tribeToTribeMessages;
        } else {
          throw new Error("Invalid chat type");
        }

        const messagesWithUserInfo = await Promise.all(
          data.map(async (msg) => ({
            ...msg,
            username: await fetchUserInfo(msg.user_id),
          }))
        );

        setMessages(messagesWithUserInfo);
      } catch (error) {
        console.error("Error fetching messages:", error);
        setError("Failed to load messages. Please try again.");
      } finally {
        setIsLoading(false);
      }
    },
    [type, tribe, otherTribe]
  );

  return { messages, setMessages, isLoading, error, fetchMessages };
};
